import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationEnd, Router, UrlTree } from '@angular/router';
import { CalcService, TextService, StorageService, SyncService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from '../../shared/location.service';
import { PRIMARY_OUTLET, INTRO_COMPLETE } from '../../shared/utils/utils';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'registration',
    templateUrl: './registration.html',
    styleUrls: ['./registration.styl']
})
export class Registration implements OnInit, OnDestroy {
    public userIsForeman = true;

    constructor(
        private calcService: CalcService, 
        private textengineService: TextService, 
        private router: Router, 
        private locationService: LocationService, 
        private storageService: StorageService, 
        private syncService: SyncService, 
        private appConfig: AppConfig
    ) { };

    ngOnInit() {
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
    }

    navigate() {
        // this.storageService.setValue(INTRO_COMPLETE, 'false')
        let url = ['intro', 'welcome'];
        this.locationService.setLocation(url, 1);
        this.router.navigate(url);
    }

    ngOnDestroy() {

    }
}
