import { Component, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import * as Collections from 'typescript-collections';
import { StorageService } from '../../../shared/storage.service';
import { MEETINGS_OUTLET, Utils } from '../../../shared/utils/utils';

@Component({
    selector: 'feedback',
    templateUrl: './feedback.html',
    styleUrls: ['./feedback.styl'],
    // animations: [
    //     trigger('routeAnimation', [
    //         state('*', style({ transform: 'translateX(100%)', opacity: 1 })),
    //         transition('void => *', [
    //             style({ transform: 'translateX(200%)', opacity: 1 }),
    //             animate(1000)
    //         ]),
    //         transition('* => void', [
    //             animate(1000, style({ transform: 'translateX(200%)', opacity: 0 }))
    //         ])
    //     ])
    // ],
    host: {
        '[style.display]': "'block'"
    }
})
export class FeedbackComponent implements OnInit, OnDestroy {
    isClosing: boolean = false;
    id: string;
    scene: Collections.Dictionary<string, string>;
    choice: number = -1;
    feedback: string = "";
    imageHeader: string = "";
    title: string = "";
    oneAtATime: boolean = true;
    narrative: string;
    choices = [];
    tabVal: any;
    @ViewChild('feedbackModal', {static: true}) childModal: CommonModalComponent;

    @HostListener('document:keydown.arrowup', ['$event'])
    @HostListener('document:keydown.arrowdown', ['$event'])
    @HostListener('document:keydown.arrowleft', ['$event'])
    @HostListener('document:keydown.arrowright', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 39:
            case 40:
                $('.nav-item:focus').next().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
            case 37:
            case 38:
                $('.nav-item:focus').prev().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
        }
    }
    constructor(private calcService: CalcService, private textEngineService: TextService, private route: ActivatedRoute, 
        private router: Router, private utils: Utils, private storageService: StorageService,
        private textHelper: TextHelperService
    ) { }

    ngOnInit() {
        this.isClosing = false;
        this.route.params.forEach(params => this.id = params['id']);
        this.scene = this.textEngineService.getScene(this.id);

        // get choice made
        this.choice = Number(this.calcService.getValue("tlinputDec" + this.id));
        this.tabVal = this.choice;
        
        this.choices = this.textHelper.getSingleSelectChoices(this.id);
        console.log ("choice: " + this.choice);
        // set feedback text
        this.title = this.scene.getValue("name");
        this.feedback = this.scene.getValue("alt" + this.choice + "_feedback");
        this.imageHeader = (this.scene.getValue("feedbackimage")) ? "assets/images/" + this.scene.getValue("feedbackimage") : "";
        this.narrative = this.scene.getValue('narrative')
    }

    ngOnDestroy() {

    }

    ngAfterViewInit() {
        this.showModal();
    }

    onClose() {
        this.isClosing = true;
        setTimeout(() => this.router.navigateByUrl('/dashboard'), 100);
        this.childModal.closeModal();
    }

    showModal() {
        this.childModal.openModal();
    }
}
