import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, StorageService, SyncService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { PLANNING_OUTLET, PLANNING_TOOL_VISITED, Utils } from '../../shared/utils/utils';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'planning-tool',
    templateUrl: './planning-tool.html',
    styleUrls: ['./planning-tool.styl'],
    providers: []
})

export class PlanningTool implements OnInit, OnDestroy {
    public userIsForeman = true;
    isPlanningToolVisited: boolean = false;

    constructor(
        private calcService: CalcService, 
        private router: Router, 
        private utils: Utils, 
        private communicator: CommunicatorService,
        private syncService: SyncService,  
        private appConfig: AppConfig,
        private storage: StorageService,
    ) { }

    ngOnInit() {
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
        this.communicator.getEmitter(PLANNING_TOOL_VISITED).subscribe(res => {
            if (res == true) {
                this.isPlanningToolVisited = true;
            }
        });
        this.storage.getValue(PLANNING_TOOL_VISITED).then(res => {
            if(res == 'true') {
                this.isPlanningToolVisited = true;
            }
        });
    }

    ngOnDestroy() {
    }
}
