import { Component, OnDestroy, OnInit } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import * as Collections from 'typescript-collections';
import { LocationService } from '../../../shared/location.service';
import { StorageService } from '../../../shared/storage.service';
import { TextHelperService as TextService } from '../../../shared/text-service.extended';
import { LinkStatus } from '../link/linkstatus';

export const MEETING_STATUS = "meetingstatus";
export const MESSAGE_STATUS = "messagestatus";

@Component({
    selector: 'messages-dashboard',
    templateUrl: './messages-dashboard.html',
    styleUrls: ['./messages-dashboard.styl'],
    providers: []
})

export class MessagesDash implements OnInit, OnDestroy {
    private content = {};
    private meetings = {};
    private messages = {};
    private scenes: Collections.Dictionary<string, any>;
    public messagesArray = [];
    public meetingsArray = [];
    private feedbackArray = [];
    private meetingStatusSubscription: Subscription;
    private messageStatusSubscription: Subscription;
    public newMeetingsCount = 0;
    public newMessagesCount = 0;
    public totalMeetings = 0;
    public totalMessages = 0;
    public hideAlert = false;
    private currentRound: string = '';

    constructor(private textengineService: TextService, private calcService: CalcService, private storageService: StorageService,
        private locationService: LocationService, private commuicator: CommunicatorService) { };

    ngOnInit() {
        this.currentRound = this.calcService.getValue("tlInputCurrentRound");
        this.fetchSceneData();
        this.hideAlert = this.locationService.getFollowerStatus() == 1
        this.initializeStatus();
        this.meetingStatusSubscription = this.commuicator.getEmitter(MEETING_STATUS).subscribe(status => {
            if (status) {
                this.newMeetingsCount = status.getNewLinksCount();
                this.totalMeetings = status.getTotalLinksCount();
            }
            // console.log("New meetings: " + this.newMeetingsCount);
        });
        this.messageStatusSubscription = this.commuicator.getEmitter(MESSAGE_STATUS).subscribe(status => {
            if (status) {
                this.newMessagesCount = status.getNewLinksCount();
                this.totalMessages = status.getTotalLinksCount();
            }
            // console.log("New messages: " + this.newMessagesCount);
        });
        // this.router.events.subscribe((event) => {
        //     if (event instanceof NavigationEnd) {
        //         this.fetchSceneData();
        //     }
        // });
    }

    ngOnDestroy() {
        this.meetingStatusSubscription.unsubscribe();
        this.messageStatusSubscription.unsubscribe();
    }

    initializeStatus() {
        this.storageService.setValueInDictionary(MEETING_STATUS, new LinkStatus());
        this.storageService.setValueInDictionary(MESSAGE_STATUS, new LinkStatus());
    }

    fetchSceneData() {
        this.scenes = this.textengineService.getScenes();
        let self = this;
        this.scenes.forEach((key => {
            // for (var key in this.scenes) {
            var localScene = [];
            // for (var property in this.scenes[key]) {
            //     if (this.scenes[key].hasOwnProperty(property)) {
            //         localScene[property] = this.scenes[key][property];
            //     }
            // }
            const _scene: Collections.Dictionary<string, any> = this.scenes.getValue(key);

            _scene.keys()
                .forEach((key) => {
                    localScene[key] = _scene.getValue(key);
                });
            
            let ID = _scene.getValue('ID')
            localScene["decisionRange"] = "tlinputDec" + ID;
            localScene["roundRange"] = "tlInputRound" + ID;
            localScene["triggerRange"] = "tloutputTrigger" + ID;
            localScene["readRange"] = "tlinputRead" + ID;
            let sceneRound = ID[ID.length - 1];

            if (localScene["PageType"] == "Read Update") {
                if (self.currentRound == sceneRound) {
                    this.messagesArray.push(localScene);
                }
            } else if ((localScene["PageType"] == "SingleSelect") || (localScene["PageType"] == "MultiSelect")) {
                if (self.currentRound == sceneRound) {
                    this.meetingsArray.push(localScene);
                }
            } else if (localScene["PageType"] == "Initiative") {
                // processed in corporate.ts
            } else if (localScene["PageType"] == "Feedback") {
                this.feedbackArray.push(localScene);
            } else {
                console.log("Unknown Page Type: " + localScene["PageType"]);
            }
        }));
        this.messagesArray = this.messagesArray.reverse();
        this.meetingsArray = this.meetingsArray.reverse();
    }
}
