import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { CalcService, NumberFormattingPipe } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { StorageService } from '../../shared/storage.service';

const CURRENT_YEAR_REF = 'tlInputTeamYear';

@Component({
  selector: 'text-metric',
  templateUrl: './text-metric.html',
  styleUrls: ['./text-metric.styl'],
  providers: [NumberFormattingPipe]
})

export class TextMetric implements OnInit, OnDestroy {
  @Input() titleKey: string;
  @Input() ref: string;
  @Input() currentYearRef: string;
  @Input() lastYearRef: string;
  @Input() format: string;
  @Input() scaler: number;
  @Input() metricPos: string;
  public showLastYearRef: boolean = false;
  @Input() showMetricSeparator: boolean = false;
  private value: string;
  private oldValue: string;
  private lastYearValue: string;
  public valueChanged: number = -1;
  private subscription: Subscription;

  constructor(private calcService: CalcService, private storageService: StorageService, private numberFormatting: NumberFormattingPipe) { }

  ngOnInit() {
    if (this.currentYearRef !== undefined) {
      this.value = this.calcService.getValueForYear(this.ref, this.currentYearRef);
      if (this.lastYearRef) {
        this.lastYearValue = this.calcService.getValueForYear(this.ref+'Opening', this.lastYearRef);
      }
    } else {
      this.value = this.calcService.getValue(this.ref);
      if (this.lastYearRef !== undefined) {
        this.lastYearValue = this.calcService.getValue(this.lastYearRef);
      }
    }
    if (this.lastYearRef) {
      this.showLastYearRef = true;
    } else {
      // check if data exists in persistent storage - page refreshed so inmemory cache is cleared
      let curYear = this.calcService.getValue(CURRENT_YEAR_REF),
        ref = this.ref + "_year_" + curYear,
        getDataPromise = this.storageService.getValue(ref);
      // if (getDataPromise === null) {
      //     this.saveMetricOldValue(this.value);
      // }
      // else {
      getDataPromise.then((val: string) => {
        if (val !== null) {
          this.lastYearValue = val;
          this.setValueChanged();
        } else {
          this.saveMetricOldValue(this.value);
        }
      })
      // }
    }
    this.setValueChanged();
    this.subscription = this.calcService.getObservable().subscribe(() => {
      // this.saveMetricOldValue(this.value);
      if (this.currentYearRef) {
        this.value = this.calcService.getValueForYear(this.ref, this.currentYearRef);
      } else {
        this.value = this.calcService.getValue(this.ref);
      }
      this.setValueChanged();
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  saveMetricOldValue(value: string) {
    let curYear = this.calcService.getValue(CURRENT_YEAR_REF),
      ref = this.ref + "_year_" + curYear;
    this.storageService.setValue(ref, value);
  }

  setValueChanged() {
    const curYear = this.calcService.getValue(CURRENT_YEAR_REF);
    // check for invalid data
    //    const errorRegex = /^(#REF\!|#VALUE\!|#DIV0\!|#NAME\!)$/i;
    //    console.log ("textmetric: " + this.ref + ", " + this.value + " : " + this.lastYearRef + ", " + this.lastYearValue + " : " + (this.ref + "_year_" + curYear) + ", " + this.dataStore.getData(this.ref + "_year_" + curYear));
    //    if (errorRegex.test(this.value) || errorRegex.test(this.lastYearValue) || errorRegex.test(this.dataStore.getData(this.ref + "_year_" + curYear))) {
    //        console.log ("error: " + this.ref + ", " + this.value + " : " + this.lastYearRef + ", " + this.lastYearValue + " : " + (this.ref + "_year_" + curYear) + ", " + this.dataStore.getData(this.ref + "_year_" + curYear));
    //        return;
    //    }
    let curValue = this.numberFormatting.parse(this.value);
    this.storageService.getValue(this.ref + "_year_" + curYear).then((val: string) => {
      if (val === undefined) {
        val = null;
      }
      const lastValue = (this.lastYearRef) ? this.numberFormatting.parse(this.lastYearValue) : this.numberFormatting.parse(val);
      if (curValue > lastValue) {
        this.valueChanged = 1;
      } else if (curValue < lastValue) {
        this.valueChanged = 2;
      } else if (curValue === lastValue) {
        this.valueChanged = 0;
      }
    });
  }
}
