import { Component, OnInit, ViewChild, Input, OnDestroy } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { RAISE_HELP } from '../utils/utils';
import { CommonModalComponent } from '../common-modal/common-modal.component';

@Component({
  selector: 'ism-help',
  templateUrl: './help.component.html',
  styleUrls: ['./help.component.styl']
})
export class HelpComponent implements OnInit, OnDestroy {
  @ViewChild('helpModal', { static: true }) public helpModalRef: CommonModalComponent;
  @Input() icon = 'fa fa-question-circle';
  @Input() buttonText = 'Help';
  @Input() showButton = true;
  helpSubscriber: Subscription;
  constructor(private calcService: CalcService, private communicator: CommunicatorService) { }

  ngOnInit() {
    // Use the following communicator observable to trigger the help with using the component provided button
    this.helpSubscriber = this.communicator.getEmitter(RAISE_HELP).subscribe(() => {
      this.showHelpAlert();
    });
  }

  ngOnDestroy() {
    this.helpSubscriber.unsubscribe();
  }

  showHelpAlert() {
    this.helpModalRef.openModal();
    this.calcService.setValue('tlInputHelp', 1);
  }
  hideHelpAlert() {
    this.helpModalRef.closeModal();
  }

}
