import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LocationService } from '../../shared/location.service';
import { StorageService } from '../../shared/storage.service';
import { INTRO_COMPLETE, SWOT_ROUTE, Utils, HIDE_DASHBOARD_METRICS, FORCE_AUTO_GROW } from '../../shared/utils/utils';
import { CalcService, TextService, SyncService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { environment } from 'src/environments/environment';
import { Subscription } from 'rxjs';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'swot',
    templateUrl: './swot.html',
    styleUrls: ['./swot.styl'],
    providers: []
})
export class SwotComponent implements OnInit, OnDestroy {
    public activeTabIndex: number = 0;
    isIntro: boolean = true;
    public userIsForeman = true;
    navIndex: any = "0"
    assertiveText: string = '';
    
    @HostListener('document:keydown.arrowup', ['$event'])
    @HostListener('document:keydown.arrowdown', ['$event'])
    @HostListener('document:keydown.arrowleft', ['$event'])
    @HostListener('document:keydown.arrowright', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 39:
            case 40:
                $('.nav-item:focus').next().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
            case 37:
            case 38:
                $('.nav-item:focus').prev().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
        }
    }
    constructor(
        private calcService: CalcService,
        private textEngineService: TextService,
        private router: Router,
        private storageService: StorageService,
        private utils: Utils,
        private locationService: LocationService,
        private syncService: SyncService,
        private activatedRoute: ActivatedRoute,
        private appConfig: AppConfig,
        private communicator: CommunicatorService
    ) {
    }

    ngOnInit() {
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
        this.isIntro = this.router.url.indexOf('intro') > -1 ? true : false;
        if(!this.isIntro){
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
        }
    }

    ngOnDestroy() {
        if(!this.isIntro){
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
        }
    }

    navigate() {
        let url = ['intro', 'goalsetting'];

        if (!this.userIsForeman) {
            return
        }

        this.locationService.setLocation(url, 1);
        this.router.navigate(url);
    }
}
