import { Component, OnInit, OnDestroy, Input, ElementRef, Output, EventEmitter, ViewChild, AfterViewInit } from '@angular/core';
import { CalcService, TextService, SyncService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { environment } from 'src/environments/environment';
import { FORCE_AUTO_GROW, Utils } from 'src/app/shared/utils/utils';
import { isUndefined } from 'util';
import { AppConfig } from 'src/app/app.config';
import { Subscription } from 'rxjs';

@Component({
    selector: 'swot-input',
    templateUrl: './swot-input.html',
    styleUrls: ['./swot-input.styl'],
    providers: []
})
export class SwotInput implements OnInit, OnDestroy {
    @Input() ref: string;
    @Input() initialTextKey: string;
    @Input() isReadOnly = false;
    @Input() _index: any;
    @ViewChild('textBox', {static : false}) textBox: ElementRef;
    
    public inputText: string;
    initialModelValue: string;

    private subscription: Subscription;
    autogrowSubscriber: Subscription;
    public userIsForeman = true;
    
    isEnable = false;
    isVisible = false;
    public set setVisibile(v) {
        this.isVisible = v;
    }

    @Output() onUpdate = new EventEmitter();
    @Output() onDelete = new EventEmitter();
    

    constructor(
        private calcService: CalcService, 
        private textEngine: TextService, 
        private syncService: SyncService,
        private communicator: CommunicatorService,
        private utils: Utils,
        public elRef: ElementRef,
        private appConfig: AppConfig
    ) { }

    ngOnInit() {
        this.updateValues();

        this.utils.getPristineModel().then((initialModel)=>{
            this.initialModelValue = initialModel.getValue(this.ref);
            this.updateValues();
        })

        this.subscription = this.calcService.getObservable().subscribe(() => {
            this.updateValues();
        })
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }

        this.autogrowSubscriber = this.communicator.getEmitter(FORCE_AUTO_GROW).subscribe(()=>{
            setTimeout(() => {
                this.autogrow();
            }, 1000);
        })
    }

    updateValues() {
        this.inputText = this.calcService.getValue(this.ref);

        if(this.initialModelValue && this.initialModelValue == this.inputText){
            this.setVisibile = false;
            let initialText = this.textEngine.getText(this.initialTextKey)
            if(initialText == this.initialTextKey || isUndefined(initialText)){
                this.inputText = '';
                this.setVisibile = false;
            }else{
                this.inputText = initialText.replace(/<br>/g, '\r\n');
                this.setVisibile = true;
                this.onBlur();
            }
        }else if(this.initialModelValue && this.initialModelValue != this.inputText && this.inputText != ''){
            this.setVisibile = true;
        }

        this.onUpdate.emit()
    }

    ngOnDestroy() {
        if(this.subscription){
            this.subscription.unsubscribe();
        }
        if(this.autogrowSubscriber){
            this.autogrowSubscriber.unsubscribe();
        }
    }

    onEditClick() {
        this.isEnable = !this.isEnable;
        if (this.isEnable) {
            this.setFocus();
        }
    }

    onDeleteClick() {
        this.calcService.setValue(this.ref, '');
        this.onDelete.emit(this._index)
        // this.isVisible = false;
    }

    onBlur() {
        this.calcService.setValue(this.ref, this.inputText);
        this.isEnable = false
    }

    autogrow() {
        let textArea = this.textBox.nativeElement;
        textArea.style.overflow = 'hidden';
        textArea.style.height = 'auto'; 
        textArea.style.height = textArea.scrollHeight > 0 ? textArea.scrollHeight + 'px' : 'auto';
    }

    setFocus() {
        setTimeout(() => {
            this.textBox.nativeElement.focus();
        }, 0);
    }

}
