import { Component, OnInit, ElementRef, OnDestroy, HostListener } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CalcService, SyncService, Constants, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import * as Collections from 'typescript-collections';
import { TextHelperService as TextService } from '../../shared/text-service.extended';
import { EOR_FEEDBACK, Utils, YEAR_OUTLET, HIDE_DASHBOARD_METRICS, FLAG_SKIP_EOR_REF } from '../../shared/utils/utils';
import { StorageService } from '../../shared/storage.service';
import { Subscription } from 'rxjs';
import { LocationService } from 'src/app/shared/location.service';
import * as $ from 'jquery';

let TABS_WIDTH = 200;
@Component({
    selector: 'endofroundfeedback',
    templateUrl: './end-of-round-feedback.html',
    styleUrls: ['./end-of-round-feedback.styl']
})

export class EndOfRoundFeedback implements OnInit, OnDestroy {
    
    tabsHeaders = [];
    scenes: Collections.Dictionary<string, any>;
    feedbackArray = [];
    typeArray = [];
    activeTabArray = [];
    activeIndex: number = 0;
    roundYearArray = [];
    feedbackYear: string;
    maxYear = 0;
    currentYear = 1;
    currentURL = "";
    el: HTMLElement;
    tabsVisible: number;
    minMarginLeft: number;
    scrollLeftDisabled = true;
    scrollRightDisabled = false;
    simComplete: number;
    isReadOnly = false;
    subscription: Subscription;
    navIndex: any = '0';
    assertiveText: string = '';
    @HostListener('document:keydown.arrowup', ['$event'])
    @HostListener('document:keydown.arrowdown', ['$event'])
    @HostListener('document:keydown.arrowleft', ['$event'])
    @HostListener('document:keydown.arrowright', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 39:
            case 40:
                $('.filterbtn:focus').next().focus();
                $('.nav-item:focus').next().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
            case 37:
            case 38:
                $('.filterbtn:focus').prev().focus();
                $('.nav-item:focus').prev().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
        }
    }

    constructor(
        private textengineService: TextService,
        private calcService: CalcService,
        private router: Router, 
        activatedRoute: ActivatedRoute,
        private utils: Utils, private syncService: SyncService,
        private storageService: StorageService,
        private elRef: ElementRef,
        private communicator: CommunicatorService,
        private locationService: LocationService
    ) {
        this.subscription = activatedRoute.params.subscribe((param: any) => {
            let activeYear = (param['year']) ? param['year'] : null;
            this.feedbackYear = activeYear;
            this.calcService.setValue("tlInputFeedbackRound", this.feedbackYear);
            this.initEOR();
        });
    }

    ngOnInit() {
        let skipEOR = +this.calcService.getValue(FLAG_SKIP_EOR_REF) == 1 ? true : false;
        if(skipEOR){
            this.onClose();
        }
        
        this.maxYear = Number(this.calcService.getValue("tlInputMaxRound"));
        this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));


        // this.activatedRoute.params.forEach(params => this.feedbackYear = params['year']);
        // this.calcService.setValue("tlInputFeedbackYear", this.feedbackYear);
        this.simComplete = Number(this.calcService.getValue('tlInputSimComplete'));
        this.el = this.elRef.nativeElement;

        this.currentURL = this.router.url;
        this.storageService.setValue(Constants.RETURN_URL, this.currentURL);
        this.isReadOnly = this.currentURL.indexOf('dashboard') != -1
        if(!this.isReadOnly){
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'true')
        }

        this.initEOR();
       
    }

    initEOR() {
        this.scenes = this.textengineService.getFeedback();
        this.feedbackArray = [];
        this.typeArray = [];
        this.tabsHeaders = [];
        this.activeTabArray = [];
        this.roundYearArray = [];

        let tlInputFeedbackRound = parseInt(this.calcService.getValue("tlInputFeedbackRound"));

        this.scenes.forEach((key, scene) => {

            // for (var key in this.scenes) {
            var localScene = [];
            // for (var property in this.scenes[key]) {
            //     if (this.scenes[key].hasOwnProperty(property)) {
            //         localScene[property] = this.scenes[key][property];
            //     }
            // }
            scene.forEach((_key, value) => {
                localScene[_key] = value;
            });

            let round = localScene["round"];
            let currentRound = (round == tlInputFeedbackRound);

            if (currentRound) {
                this.roundYearArray.push(currentRound);
                this.activeTabArray.push(false);
                this.feedbackArray.push(localScene);
                this.typeArray.push(localScene["subpage"].toLowerCase());
                this.tabsHeaders.push(localScene["name"]);
            }
            // }
        });
    }

    onClose() {
        let tlInputFeedbackRound = parseInt(this.calcService.getValue("tlInputFeedbackRound"));

            // let url = this.utils.stripOutletFromUrl(this.router.url, YEAR_OUTLET);
            //this.router.navigateByUrl(this.utils.stripChildRouteFromUrl(url, EOR_FEEDBACK));
            let currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
            // this.currentYear = this.currentYear + 1;
            let tlInputPasswordAfterFeedback_R1 = Number(this.calcService.getValue("tlInputPasswordAfterFeedback_R1"));
            let tlInputPasswordAfterFeedback_R2 = Number(this.calcService.getValue("tlInputPasswordAfterFeedback_R2"));
            if ((tlInputFeedbackRound == 1 && tlInputPasswordAfterFeedback_R1 == 1) || (tlInputFeedbackRound == 2 && tlInputPasswordAfterFeedback_R2 == 1)) {
                // let locationNumber = this.calcService.getValue("tlInputPasswordLocation");
                // locationNumber = parseInt(locationNumber)+1;
                let locationNumber: any = Number(this.calcService.getValue("tlInputCurrentRound"));
                locationNumber = (parseInt(locationNumber) * 2) + 1;
                this.calcService.setValue('tlInputPasswordLocation', locationNumber);
                let url = ['password'];
                this.router.navigate(url);
            }
            else {
                if (tlInputFeedbackRound < this.maxYear) {
                    let url = this.utils.stripOutletFromUrl(this.router.url, YEAR_OUTLET);
                    this.router.navigateByUrl(this.utils.stripChildRouteFromUrl(url, EOR_FEEDBACK));
                } else {
                    currentYear = Math.min(currentYear + 1, this.maxYear);
                    this.calcService.setValue("tlInputCurrentRound", currentYear);
                    // let url = this.utils.stripOutletFromUrl(this.router.url, YEAR_OUTLET);
                    // this.router.navigateByUrl(this.utils.stripChildRouteFromUrl(url, EOR_FEEDBACK));
                    this.router.navigate(['dashboard/congratulations'])

                }
            }

    }

    setActiveIndex(index: string): any {
        this.activeIndex = parseInt(index);
    }

    onNext() {
        this.activeIndex = Math.min(this.activeIndex + 1, (this.feedbackArray.length - 1));
    }

    onBack() {
        this.activeIndex = Math.max(0, this.activeIndex - 1);
    }

    getFilters() {
        // return this.filters.map(i => 'cat-' + i).join(' ');
    }

    scroll(direction) {
        this.updateElemCount();
        const navHeaderElm: any = this.el.getElementsByClassName('eor-nav')[0],
            tabsLength: number = navHeaderElm.querySelectorAll('.eor-nav-item').length;
        let marginLeft = parseInt(window.getComputedStyle(navHeaderElm)['margin-left'], 10);
        // scroll by 3 elements with
        if (direction !== 0) { // scroll right
            let scrollByElems = 0;
            const nextScroll = tabsLength - (parseInt((-marginLeft / TABS_WIDTH) + '', 10) + 10);
            scrollByElems = (nextScroll >= 0) ? 3 : (3 + nextScroll);
            marginLeft = marginLeft - (scrollByElems * TABS_WIDTH);
            if (scrollByElems === 0) {
                marginLeft = this.minMarginLeft;
            }
            marginLeft = (marginLeft <= this.minMarginLeft) ? this.minMarginLeft : marginLeft;
        } else { // scroll left
            marginLeft = marginLeft + (3 * TABS_WIDTH);
            marginLeft = (marginLeft >= 0) ? 0 : marginLeft;
        }
        this.scrollLeftDisabled = (marginLeft < 0) ? false : true;
        this.scrollRightDisabled = (marginLeft > this.minMarginLeft) ? false : true;
        navHeaderElm.style.marginLeft = marginLeft + 'px';
    }

    updateElemCount() {
        // -1068px
        const navHeaderElm: any = this.el.getElementsByClassName('eor-nav')[0];
        const nodes: any = this.el.getElementsByClassName('eor-nav-item'),
            elems: Array<HTMLElement> = Array.prototype.slice.call(nodes);
        this.tabsVisible = 0;
        elems.forEach((elem, elemIndex) => {
            if (!!(elem.offsetWidth || elem.offsetHeight || elem.getClientRects().length)) {
                // this.investmentsArray[0][elemIndex].isSelected = true;
                this.tabsVisible++;
            }
        });
        // -12 is the offset by which the last tabs is cutoff
        this.minMarginLeft = (((this.tabsVisible - (navHeaderElm.offsetWidth/TABS_WIDTH)) * TABS_WIDTH)) * -1;
    }

    navigateToRound(e, _id: any) {
        // this.feedbackYear = _id;
        // this.calcService.setValue("tlInputFeedbackYear", this.feedbackYear);
        // this.initEOR();
        this.assertiveText = 'Year' + _id + (e.target.classList.contains('active') ? 'selected' : 'not selected');
        const url = '/dashboard/endofroundfeedback/' + _id; 
        this.locationService.setLocation(url, 0)
        this.router.navigate([url]);
    }

    
    ngOnDestroy() {
        if(this.isReadOnly){
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false')
        }
        this.subscription.unsubscribe();
    }
}
