import { Component, OnDestroy, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { HIDE_DASHBOARD_METRICS } from '../shared/utils/utils';
import { StorageService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
// import { SyncService } from '../connect/sync.service';

@Component({
    selector: 'intro-component',
    templateUrl: './intro.component.html',
    styleUrls: ['./intro.component.styl']
})
export class IntroComponent implements OnInit, OnDestroy, AfterViewInit {

    constructor(
        private storageService: StorageService,
        private communicator: CommunicatorService
    ) { };

    ngOnInit() {
    }
    
    ngAfterViewInit(){
        this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'true');
    }
    
    ngOnDestroy() {
        this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
    }
}
