import { Component, Input, OnInit, AfterViewInit } from '@angular/core';
import { CalcService, TextService, NumberFormattingPipe } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';


@Component({
    selector: 'feedbacktextandchart',
    templateUrl: './feedbacktextandchart.html',
    styleUrls: ['./feedbacktextandchart.styl'],
    providers: []
})

export class FeedbackTextAndChart implements OnInit, AfterViewInit {
    @Input() index;
    @Input() feedback = {};
    @Input() rangeref = "";
    @Input() categorylabels = "";
    @Input() serieslabels = "";
    @Input() year = "";
    @Input() numberformat = "";
    @Input() charttitle = "";
    @Input() chartoptions: any = {};
    @Input() min = "";
    @Input() max = "";
    @Input() linewidth = "1";

    public title = "";
    public narrative = "";
    private chart;
    public chartType = "";
    public charttopper = "";

    private isHidden = true;
    private isViewed = false;

    localRangeRefArray: any;
    localCategoryLabelsArray: any;
    localSeriesLabelsArray: any;

    chartRangeRef: any;
    chartXAxis: any;
    chartSeriesOptions: any;
    chartYAxis: any;

    constructor(private calcService: CalcService, private textService: TextService, private numberFormatting: NumberFormattingPipe) { };

    ngOnInit() {
        let narrativeChunk: string = "";

        this.title = this.feedback["name"];

        // this.chart = this.sanitizer.bypassSecurityTrustHtml(this.feedback["chart"]);
        this.rangeref = this.feedback["chartranges"];
        this.categorylabels = this.feedback["chartcategories"];
        this.serieslabels = this.feedback["chartseries"];
        this.year = this.feedback["chartyear"];
        this.numberformat = this.feedback["chartnumberformat"];
        this.charttitle = this.feedback["charttitle"];
        this.charttopper = this.feedback["charttopper"];
        // console.log('feedback text' + this.feedback['chartoptions']);
        if (this.feedback["chartoptions"] && this.feedback["chartoptions"] !== "") {
            try {
                this.chartoptions = JSON.parse(this.feedback["chartoptions"].replace(/'/g, '"'));
                // console.log('JSON - ', this.chartoptions);
            }
            catch (e) {
                this.chartoptions = {};
                // console.log(e);
            }
        }
        this.min = this.feedback["min"];
        this.max = this.feedback["max"];
        this.linewidth = this.feedback["linewidth"];
        this.chartType = this.feedback["chart"];
        // if (this.title == 'Sources of Revenue') {
        //     this.numberformat = '0.0%';
        // }

        if (this.chartType !== 'heatmap') {
            this.processComponentInputs();
        }

        _.merge(this.chartoptions, { legend: { align: 'right', width: 130, layout: 'vertical', verticalAlign: 'middle', itemMarginBottom: 10 } });

        if (this.chartType === 'heatmap') {
            this.chartoptions = JSON.stringify(this.chartoptions);
        }

        let index: number = 1;
        narrativeChunk = this.feedback["narrative" + index];

        while ((narrativeChunk != undefined) && (index < 100)) {
            let conditional: any = true;
            let conditionalChunk = this.feedback["condition" + index];

            if (conditionalChunk != undefined) {
                if (this.year != "") {
                    conditional = this.calcService.getValueForYear(conditionalChunk, this.year);
                } else {
                    conditional = this.calcService.getValue(conditionalChunk);
                }
            }
            if (conditional && conditional != "0") {
                this.narrative += narrativeChunk;
            }

            index++;
            narrativeChunk = this.feedback["narrative" + index];
        }

        if (this.narrative != undefined) {
            let startLoc = this.narrative.indexOf("{{");
            index = 0;

            while ((startLoc != -1) && (index < 100)) {
                let endLoc = this.narrative.indexOf("}}");
                if (endLoc != -1) {
                    let lookupVal = this.narrative.substr(startLoc + 2, ((endLoc - 1) - startLoc - 1)).trim();
                    this.narrative = this.narrative.substr(0, startLoc) + this.calcService.getValue(lookupVal) + this.narrative.substr(endLoc + 2);
                }
                startLoc = this.narrative.indexOf("{{");
                index++;
            }
        }

        if (this.charttopper != undefined) {
            let startLoc = this.charttopper.indexOf("{{");
            index = 0;

            while ((startLoc != -1) && (index < 100)) {
                let endLoc = this.charttopper.indexOf("}}");
                if (endLoc != -1) {
                    let lookupVal = this.charttopper.substr(startLoc + 2, ((endLoc - 1) - startLoc - 1)).trim();
                    this.charttopper = this.charttopper.substr(0, startLoc) + this.calcService.getValue(lookupVal) + this.charttopper.substr(endLoc + 2);
                }
                startLoc = this.charttopper.indexOf("{{");
                index++;
            }
        }
    }

    ngAfterViewInit(){
        this.forceResize()
    }

    forceResize(){
        setTimeout(()=>{
            if(typeof(Event) === 'function') {
              // modern browsers
              window.dispatchEvent(new Event('resize'));
            }else{
              // for IE and other old browsers
              // causes deprecation warning on modern browsers
            //   var evt = window.document.createEvent('UIEvents'); 
            //   evt.initUIEvent('resize', true, false, window, 0); 
            //   window.dispatchEvent(evt);
            }
        },1000);
    }

    getChartRangeRef() {
        let rangeref = [];
        switch (this.chartType) {
            case 'basicbar':
            case 'basicline':
            case 'basiccolumn':
            case 'stackedcolumn':
                rangeref = _.chunk(this.localRangeRefArray, this.localCategoryLabelsArray.length);
                this.chartRangeRef = rangeref;
                break;
            default:
                this.chartRangeRef = this.localRangeRefArray;
                break;
        }
    }

    getChartSeries() {
        const series = [];
        switch (this.chartType) {
            case 'basicbar':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'bar', name: n });
                });
                this.chartSeriesOptions = series;
                break;
            case 'basicline':
                _.each(this.localSeriesLabelsArray, (n) => {
                    if (n.indexOf('tlInput') > -1 || n.indexOf('tlInput') > -1) {
                        n = this.calcService.getValue(n);
                    }
                    n = n ? (n + '') : '';
                    series.push({ type: 'line', name: n });
                });
                this.chartSeriesOptions = series;
                break;
            case 'basiccolumn':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'column', name: n });
                });
                this.chartSeriesOptions = series;
                break;
            case 'stackedcolumn':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'stackedcolumn', name: n });
                });
                this.chartSeriesOptions = series;
                break;
            default:
                this.chartSeriesOptions = this.localSeriesLabelsArray;
                break;
        }
    }

    getChartCategories() {
        const categories = { categories: null };
        categories.categories = this.localCategoryLabelsArray;
        this.chartXAxis = categories;
    }

    getYAxisData() {
        this.chartYAxis = {
            labels: {
                format: '{value:' + this.numberformat + '}'
            },
            title: { enabled: false },
            alternateGridColor: 'rgb(240, 241, 242)'
        };

        switch (this.chartType) {
            case 'stackedcolumn':
                this.chartYAxis['stackLabels'] = {
                    enabled: false
                };
                break;
        }
    }

    getChartOptions() {
        const self = this
        let chartOptions: any = {};
        switch (this.chartType) {
            case 'basicbar':
                chartOptions = {
                    plotOptions: {
                        bar: {
                            dataLabels: {
                                enabled: false,
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    }
                };
                break;
            case 'basicline':
                chartOptions = {
                    plotOptions: {
                        line: {
                            dataLabels: {
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    }
                };
                break;
            case 'basiccolumn':
                chartOptions = {
                    plotOptions: {
                        column: {
                            dataLabels: {
                                enabled: false,
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    }
                };
                break;
            case 'stackedcolumn':
                chartOptions = {
                    plotOptions: {
                        column: {
                            dataLabels: {
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    },
                    tooltip: {
                        headerFormat: '<b>{point.x}</b><br/>',
                        pointFormatter: function () {
                            if (self.numberformat) {
                                return this.series.name + ": " + self.numberFormatting.transform(this.y, self.numberformat) + "<br />" + "Total: " + self.numberFormatting.transform(this.total, self.numberformat);
                            } else {
                                return this.series.name + ": " + this.y + "<br/>Total: " + this.stackTotal;
                            }
                        }
                    }
                };
                break;
        }
        _.merge(this.chartoptions, chartOptions);
    }

    processComponentInputs() {
        let self = this,
            localRangeRef = self.rangeref,
            localCategoryLabels = self.categorylabels,
            localSeriesLabels = self.serieslabels;

        if (localRangeRef != undefined && localRangeRef != null) {
            localRangeRef = self.rangeref.toString();
            localRangeRef = localRangeRef.replace(/\[|\]/g, '');
            localRangeRef = localRangeRef.replace(/ /g, '');
            this.localRangeRefArray = localRangeRef.split(",");
        }

        if (localCategoryLabels != undefined && localCategoryLabels != null) {
            localCategoryLabels = self.categorylabels.toString();
            localCategoryLabels = localCategoryLabels.replace(/\[|\]/g, '');
            // localCategoryLabels = localCategoryLabels.replace(/ /g, '');
            this.localCategoryLabelsArray = localCategoryLabels.split(",").map(function(item) {
                return item.trim();
            });

            for (let i = 0; i < this.localCategoryLabelsArray.length; i++) {
                let tmpLabel = this.textService.getText(this.localCategoryLabelsArray[i]);
                if (tmpLabel != undefined) {
                    this.localCategoryLabelsArray[i] = tmpLabel;
                }
            }
        }

        if (localSeriesLabels != undefined && localSeriesLabels != null) {
            localSeriesLabels = self.serieslabels.toString();
            localSeriesLabels = localSeriesLabels.replace(/\[|\]/g, '');
            // localSeriesLabels = localSeriesLabels.replace(/ /g, '');
            this.localSeriesLabelsArray = localSeriesLabels.split(",").map(function(item) {
                return item.trim();
            });

            for (let i = 0; i < this.localSeriesLabelsArray.length; i++) {
                let tmpLabel = this.textService.getText(this.localSeriesLabelsArray[i]);
                if (tmpLabel != undefined) {
                    this.localSeriesLabelsArray[i] = tmpLabel;
                }
            }

            // for (let i = 0; i < this.localSeriesLabelsArray.length; i++) {
            //     this.localSeriesLabelsArray[i] = this.localSeriesLabelsArray[i] + '';
            //     if (this.localSeriesLabelsArray[i].indexOf('tlInput') > -1 || this.localSeriesLabelsArray[i].indexOf('tlInput') > -1) {
            //         this.localSeriesLabelsArray[i] = this.calcService.getValue(this.localSeriesLabelsArray[i]);
            //     }
            // }
        }

        this.getChartRangeRef();
        this.getChartSeries();
        this.getChartCategories();
        this.getYAxisData();
        this.getChartOptions();
    }
}
