import { Component, OnInit, Output, EventEmitter, ViewChild, Input, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { CountdownComponent, CountdownEvent, CountdownConfig } from 'ngx-countdown';
import { AppConstants } from '../../../app-constants';
import { Subscription } from 'rxjs';

@Component({
  selector: 'ism-timer',
  templateUrl: './linktimer.component.html',
  styleUrls: ['./linktimer.component.styl']
})
export class LinkTimerComponent implements OnInit, OnDestroy {
  @Input() timerValue;
  @Input() storageKey;
  @Output() onTimeUp = new EventEmitter();
  @ViewChild('countdown', { static: false }) public counter: CountdownComponent;

  timerConfig: CountdownConfig;
  isInitialized = false;
  isCompleted = false;
  timerTempValue;

  constructor(
    private router: Router,
    private storageService: StorageService, 
    private communicator: CommunicatorService,
    private cdRef: ChangeDetectorRef
  ) { }



  ngOnInit() {
    this.initialize().then(() => {
      this.isInitialized = true;
    });
  }

  ngOnDestroy() {
  }

  initialize(): Promise<any> {
    //Check if timer completed
    return this.storageService.getValue(this.storageKey + AppConstants.TIMER.TIMER_COMPLETE).then((res) => {
      // if completed set to 0
      if (res == 'true') {
        this.timerValue = '0'
        this.onTimeUp.emit();
      } else {
        //if not completed then check if it has already started
        return this.storageService.getValue(this.storageKey + AppConstants.TIMER.TIMER_STARTED).then((res) => {
          // if it has already started then fetch the stored time
          if (res == 'true') {
            return this.storageService.getValue(this.storageKey + AppConstants.TIMER.TIME_REMAINING).then((res) => {
              if (res && res.valueOf() > 0) {
                this.timerValue = res.toString();
              }
            })
          } else {
            //if not started then timerValue is the one that is provided as Input
            // do nothing
          }
        })
      }
    })
  }


  handleEvent(event: CountdownEvent) {
    if (event.action == 'start') {
      this.storageService.setValue(this.storageKey + AppConstants.TIMER.TIMER_STARTED, 'true');
    }

    if (event.action === 'notify') {
      //Store time every few seconds
      this.storeCurrentTimerValue(event.left);
    }

    if (event.action === 'done') {
      this.onTimeUp.emit();
      this.isCompleted = true;
      this.storageService.setValue(this.storageKey + AppConstants.TIMER.TIMER_COMPLETE, 'true');
    }

  }

  storeCurrentTimerValue(time) {
    let value = time / 1000; // since time receive is in miliseconds
    this.timerTempValue = value;

    // store every 30 seconds
    if (value >= 0 && value % 6 == 0) {
      this.storageService.setValue(this.storageKey + AppConstants.TIMER.TIME_REMAINING, JSON.stringify(value));
    }
  }

  updateTimer(time) {
    this.isInitialized = false;
    this.timerValue = null;
    this.cdRef.detectChanges();
    this.timerValue = time;
    if (this.isCompleted) {
      this.isCompleted = false;
      this.storageService.setValue(this.storageKey + AppConstants.TIMER.TIMER_COMPLETE, 'false');
    }
    this.isInitialized = true;
    this.cdRef.detectChanges();

  }
}
