import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { LoggerService } from '@btsdigital/pulseutilities';
import { Subscription } from 'rxjs';
import { BootstrapService } from '../bootstrap.service';
import { StorageService } from '../storage.service';
import { INTRO_COMPLETE } from '../utils/utils';
@Component({
  selector: 'splash',
  templateUrl: './splash.component.html',
  styleUrls: ['./splash.component.styl']
})

export class SplashComponent implements OnInit, OnDestroy {
  private modelProgressSubscription: Subscription;
  progress = 0;
  progressMessage: any;

  constructor(private bootstraper: BootstrapService, private router: Router, private logger: LoggerService,
    private storageService: StorageService, private communicator: CommunicatorService) { }

  ngOnInit() {
    if (!this.bootstraper.appReady) {
      this.modelProgressSubscription = this.communicator
        .getEmitter(Constants.MODEL_LOAD_PROGRESS)
        .subscribe((progress) => this.progress = progress);

      this.bootstraper
        .init()
        .then(() => {
          this.appReady();
        });
    }else{
      this.appReady();
    }
  }

  ngOnDestroy(){
    if(this.modelProgressSubscription){
      this.modelProgressSubscription.unsubscribe();
    }
  }

  private appReady() {
    this.logger.log('Model loaded');
    let returnUrl: string;
    this.storageService.getValue(Constants.RETURN_URL, true)
      .then(url => {
        url = (url === null) ? '/dashboard' : url;
        returnUrl = url + '';
        this.storageService.getValue(INTRO_COMPLETE).then((val: string) => {
          if (val === 'true') {
              this.router.navigateByUrl(returnUrl);
          }
          else {
            this.router.navigateByUrl('/intro/registration');
          }
        })

      })
      .catch(err => {
        this.router.navigateByUrl('');
      });
  }
}
