import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ANALYSIS_ROUTE, EOR_FEEDBACK, MEETINGS_OUTLET, PLANNING_OUTLET, RESOURCES_ROUTE, SWOT_ROUTE, TUTORIALS_ROUTE, Utils, HIDE_NAVIGATION, SALESMARKETTING_ROUTE, RESEARCHDEV_ROUTE, HUMANRESOURCES_ROUTE, OPERATIONS_ROUTE, SECONDARY_OUTLET, CORPORATE_ROUTE, PLANNING_TOOL_VISITED } from '../../../shared/utils/utils';
import { CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';

@Component({
    templateUrl: './routes.html',
    styleUrls: ['./routes.styl'],
})
export class DashboardRoute implements OnInit, OnDestroy {
    private subscription: Subscription;
    public isPlanningToolActive: boolean = false;
    public isMessagesActive: boolean = false;
    private isTutorialsActive: boolean = false;
    private isResourcesActive: boolean = false;
    public isAnonPaneActive: boolean = false;
    public hideNavigation: boolean = false;
    // showSecondaryNav = false;
    secondaryNavActive = false;
    showPrimaryOutlet: boolean;
    constructor(private router: Router, private utils: Utils,
        private communicator: CommunicatorService,
        private storage: StorageService
    ) { }

    ngOnInit() {
        this.subscription = this.router.events.subscribe(event => {
            if (event instanceof NavigationEnd) {
                this.updateStatus();
            }
        });

        this.communicator.getEmitter(HIDE_NAVIGATION).subscribe((res) => {
            this.hideNavigation = (res + '' === 'true') ? true : false;
        })

        this.updateStatus();
    }

    updateStatus() {
        this.isMessagesActive = this.utils.isOutletRouteActive(MEETINGS_OUTLET, this.router.url);
        // this.isPlanningToolActive = this.utils.isOutletRouteActive(PLANNING_OUTLET, this.router.url);
        // this.isAnonPaneActive = this.getAnonPaneActiveStatus();
        // this.showSecondaryNav = this.getSecondaryNavStatus();
        this.secondaryNavActive = this.utils.isOutletRouteActive(SECONDARY_OUTLET, this.router.url);
    }

    // getAnonPaneActiveStatus(): boolean {
    //     let outlets: Array<string> = [
    //         TUTORIALS_ROUTE,
    //         RESOURCES_ROUTE,
    //         EOR_FEEDBACK,
    //         SWOT_ROUTE,
    //         ANALYSIS_ROUTE,
    //     ],
    //         isActive = false;

    //     outlets.forEach(outlet => {
    //         let result = this.utils.isChildRouteActive(outlet, this.router.url, true);
    //         if (result) {
    //             isActive = true;
    //         }
    //     });

    //     return isActive;
    // }

    // getSecondaryNavStatus() {
    //     let outlets: Array<string> = [
    //         SALESMARKETTING_ROUTE,
    //         RESEARCHDEV_ROUTE,
    //         HUMANRESOURCES_ROUTE,
    //         OPERATIONS_ROUTE,
    //         CORPORATE_ROUTE
    //     ],
    //         isActive = false;

    //     outlets.forEach(outlet => {
    //         let result = this.utils.isRouteActive(outlet, this.router.url);
    //         if (result) {
    //             isActive = true;
    //         }
    //     });
    //     return isActive;
    // }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }

    onActivate(event : any) {
        this.storage.setValue(PLANNING_TOOL_VISITED, 'true');
        this.communicator.trigger(PLANNING_TOOL_VISITED, true);
        this.showPrimaryOutlet = true;
    }

    onDeactivate(event : any) {
        this.showPrimaryOutlet = false;
    }
}
