import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, TextService, SyncService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from '../../shared/location.service';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'intro-text-component',
    templateUrl: './introtext.html',
    styleUrls: ['./introtext.styl']
})
export class IntroText implements OnInit, OnDestroy {

    public content: any = {};
    public userIsForeman = true;

    constructor(
        private calcService: CalcService, 
        private textengineService: TextService, 
        private router: Router, 
        private locationService: LocationService, 
        private syncService: SyncService, 
        private appConfig: AppConfig
    ) { };

    ngOnInit() {
        this.setUpdatedText();
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
    }

    setUpdatedText() {
        this.content.title = this.textengineService.getText("IntroBlogTitle");
        this.content.body = this.textengineService.getText("IntroBlogText");
        this.content.imagePath = "assets/images/" + this.textengineService.getText("IntroBlogImage");
        this.content.chairmanImagePath = "assets/images/chairmanIcon.jpg";

        if (this.content.body != undefined) {
            let startLoc = this.content.body.indexOf("{{");
            let index = 0;

            while ((startLoc != -1) && (index < 100)) {
                let endLoc = this.content.body.indexOf("}}");
                if (endLoc != -1) {
                    let lookupVal = this.content.body.substr(startLoc + 2, ((endLoc - 1) - startLoc - 1)).trim();
                    this.content.body = this.content.body.substr(0, startLoc) + this.calcService.getValue(lookupVal) + this.content.body.substr(endLoc + 2);
                }
                startLoc = this.content.body.indexOf("{{");
                index++;
            }
        }
    }

    navigate() {
        // [routerLink] = "['/intro','email']
        // this.router.navigateByUrl(DASHBOARD_PATH);
        if (!this.userIsForeman) {
            return
        }
        let url = ['intro', 'email'];
        this.locationService.setLocation(url, 1);
        this.router.navigate(url);
    }

    ngOnDestroy() {

    }
}
