import { Component, OnInit, Input } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'ism-calc-toggle',
  templateUrl: './calc-toggle.component.html',
  styleUrls: ['./calc-toggle.component.styl']
})
export class CalcToggleComponent implements OnInit {
  @Input() ref: string
  @Input() minValue: any = 0
  @Input() maxValue: any = 1
  value: boolean;
  subscription: Subscription

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.getValue();
    this.subscription = this.calcService.getObservable().subscribe(()=>{
      this.getValue();
    })
    
  }
  
  getValue(){
    this.value = this.calcService.getValue(this.ref) == this.maxValue;
  }

  toggleValue(){
    // not toggling as calcService will cause getvalue function to call again
    // this.value = !this.value; 

    let val = !this.value ? this.maxValue : this.minValue
    this.calcService.setValue(this.ref, val)
  }

}
