import { Component, HostListener, OnInit, ViewChild } from '@angular/core';
import { CommunicatorService, StorageService, ConnectService, SyncService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Subscription } from 'rxjs';
import { APP_READY, AppConstants } from './shared/utils/utils';
import { Router, NavigationEnd } from '@angular/router';
import { BootstrapService } from './shared/bootstrap.service';
import { AppConfig } from './app.config';

@Component({
  selector: 'my-app',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.styl']
})
export class AppComponent implements OnInit {
  isAppReady: boolean = false;
  initUrlChange: boolean = false;
  subscription: Subscription;
  @ViewChild('alertDialog', { static: true }) alertDialogModalRef: ModalDirective;
  @ViewChild('sendMessageDialogue', { static: true }) sendMessageDialogueModalRef: ModalDirective;
  userDetail: any;
  isChecked = false;
  sendingMessage = '';

  constructor(private bootstraper: BootstrapService, private storageService: StorageService, private communicator: CommunicatorService, private router: Router, private connectService: ConnectService, private syncService: SyncService, private appConfig: AppConfig) { }

  ngOnInit() {
    // if (this.appConfig.configuration && this.appConfig.configuration.connectToPulse && this.appConfig.configuration.CDN0LOCAL1LOCALHOST2 === 0) {
    //   if (!(localStorage.getItem('languageSelect'))) {
    //     const token = location.href.split('?');
    //     if (token.length > 0) {
    //       window.location.href = '/login.html?' + token[1];
    //     }
    //   }
    // }
    this.bootstraper.initAppConfig();
    this.subscription = this.communicator.getEmitter(APP_READY).subscribe((value) => {
      this.isAppReady = ((value + '') === 'true') ? true : false;

      // if (!this.initUrlChange) {
      //   this.router.events.subscribe(val => {
      //     if (val instanceof NavigationEnd) {
      //       if (AppConstants.TRACKQUESTION_URL && AppConstants.TRACKQUESTION_URL.indexOf(val.url) !== -1) {
      //         this.syncService.setIsUserForeman().then(x => {
      //           const userandForManDetails = this.syncService.getForeMan();
      //           if (userandForManDetails.participantId && userandForManDetails.foremen) {
      //             for (let i = 0; i < userandForManDetails.foremen.length; i++) {
      //               if (userandForManDetails.participantId === userandForManDetails.foremen[i].foremanId) {
      //                 this.connectService.urlChanged(userandForManDetails.foremen[i].trackQuestionName, val.url);
      //               }
      //             }
      //           }
      //         })
      //       }
      //     }
      //   });
      //   this.initUrlChange = true;
      // }
    });

    this.communicator.getEmitter(AppConstants.URL_CHANGED).subscribe((value) => {
      if (value == '/dashboard') {
        this.bootstraper.appReady = true;
      }
      this.router.navigateByUrl(value);
    });

    this.communicator.getEmitter('alertUserLoggedIn').subscribe(
      (res) => {
        this.userDetail = JSON.parse(localStorage.getItem('user'));
        this.alertDialogModalRef.show();
      }
    );
    this.communicator.getEmitter('sendMessage').subscribe(
      (res) => {
        this.sendingMessage = res;
        this.sendMessageDialogueModalRef.show();
      }
    );
  }

  dontShowPopup($event, mode, action) {
    if (mode !== '') {
      if (this.isChecked) {
        localStorage.setItem('alreadyUserLoggedIn', 'true');
      }
      (action === 'sendMessage') ? this.sendMessageDialogueModalRef.hide() : this.alertDialogModalRef.hide();
    } else {
      this.isChecked = $event.checked;
    }
  }
}
