import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Input, OnInit, ElementRef, Output } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { EventEmitter } from 'events';
import * as _ from 'lodash';
import { Selection_Pending } from 'src/app/shared/utils/utils';
declare const $: any;

@Component({
  selector: 'app-dragdrop',
  templateUrl: './dragdrop.component.html',
  styleUrls: ['./dragdrop.component.styl']
})
export class DragdropComponent implements OnInit {
  @Input() goals: Array<any> = [];
  @Input() isDisabled = false;
  @Input() lables: Array<string>;
  @Input() ranges: Array<string>;
  @Input() yearRef: string;
  // @Input() selectionPending: false;

  data: any;
  originalIndex: number;
  

  constructor(
    private calcService: CalcService,
    private elemRef: ElementRef,
    private communicator: CommunicatorService
  ) { }

  ngOnInit() {
    if (this.goals.length === 0) {
      this.goals = this.processItems();
    }
  }

  processItems() {
    let _items = [];
    let len = this.ranges.length;
    for (let i = 0; i < len; i++) {
      let obj = {
        id: i + 1,
        text: this.calcService.getValue(this.ranges[i]),
        tabindex: this.isDisabled ? -1 : 0,
        range: this.ranges[i]
      }
      _items.push(obj);
    }
    // this.sortByKey(_items, 'order');
    return _items;
  }

  // processItems() {
  //   let _items = [];
  //   for (let i = 0; i < this.lables.length; i++) {
  //     let obj = {
  //       id: i + 1,
  //       text: this.lables[i],
  //       tabindex: this.isDisabled ? -1 : 0,
  //       label: this.lables[i],
  //       range: this.ranges[i],
  //       target: this.targetRange[i].rangeRef,
  //       targetFormat: this.targetRange[i].format,
  //       order: this.calcService.getValueForYear(this.ranges[i], this.yearRef)
  //     }
  //     _items.push(obj);
  //   }
  //   this.sortByKey(_items, 'order');

  //   return _items;
  // }

  // sortByKey(array, key) {
  //   return array.sort(function (a, b) {
  //     var x = a[key]; var y = b[key];
  //     return ((x < y) ? -1 : ((x > y) ? 1 : 0));
  //   });
  // }

  handleKeyboardEvent(event: KeyboardEvent, index: number) {
    const goals = this.elemRef.nativeElement.querySelectorAll(`li.goal-item`);
    const totalGoals = goals.length - 1;
    switch (event.keyCode) {
      case 40: // down arrow
        let goalIndexUp: number;
        if (index === totalGoals) {
          goalIndexUp = 0;
        } else {
          goalIndexUp = (index + 1);
        }
        if (!this.isAnyGoalSelected()) {
          $(goals[goalIndexUp])[0].focus();
        } else {
          this.moveGoal(index, goalIndexUp);
        }
        event.preventDefault();
        event.stopPropagation();
        break;
      case 38: // up arrow
        let goalIndexDown: number;
        if (index === 0) {
          goalIndexDown = totalGoals;
        } else {
          goalIndexDown = (index - 1);
        }
        if (!this.isAnyGoalSelected()) {
          $(goals[goalIndexDown])[0].focus();
        } else {
          this.moveGoal(index, goalIndexDown);
        }
        event.preventDefault();
        event.stopPropagation();
        break;
      case 32: // space
      case 13: // enter
        event.preventDefault();
        event.stopPropagation();
        const isAnySelected = this.isAnyGoalSelected();
        _.each(this.goals, (n) => {
          n.isSelected = false;
        });
        if (!isAnySelected) {
          this.goals[index].isSelected = true;
          this.originalIndex = index;
        } else {
          this.setGoals();
        }
        break;
      case 27: // escape key
        this.escapeFunction(index);
        break;
      case 9: // tab key
        this.escapeFunction(index);
        break;
    }
  }

  escapeFunction(index) {
    const isAnySelectedEsc = this.isAnyGoalSelected();
    if (isAnySelectedEsc) {
      _.each(this.goals, (n) => {
        n.isSelected = false;
      });
      moveItemInArray(this.goals, index, this.originalIndex);
    }
  }

  setTabIndex(index: number) {
    _.each(this.goals, (n) => {
      n.tabindex = -1;
    });
    this.goals[index].tabindex = 0;
  }

  isAnyGoalSelected(): boolean {
    let isSelected = false;
    _.each(this.goals, (n) => {
      if (n.isSelected) {
        isSelected = true;
       this.communicator.trigger(Selection_Pending,'true');
      }
    });
    return isSelected;
  }

  moveGoal(oldIndex: number, newIndex: number) {
    moveItemInArray(this.goals, oldIndex, newIndex);
    setTimeout(() => {
      const goals = this.elemRef.nativeElement.querySelectorAll(`li.goal-item`);
      $(goals[newIndex])[0].focus();
    }, 10);
    this.communicator.trigger(Selection_Pending,'true');
  }

  drop(event: CdkDragDrop<any[]>) {
    this.moveGoal(event.previousIndex, event.currentIndex);
    this.setGoals();
  }

  focusGained(goal: any, index: number) {
    if (!goal.isSelected) {
      const goalTarget = this.calcService.getValue(goal.target);
    }
  }

  setGoals() {
    // Set DragDrop Values
    for (let i = 0; i < this.ranges.length; i++) {
      try {
        const range = this.ranges[i];
        this.calcService.setValueForYear(range, this.goals[i].text, this.yearRef);
      } catch (e) { }
    }
  }
}
