import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { DASHBOARD_PATH } from '../../shared/utils/utils';

@Component({
    selector: 'planning-tool-nav',
    templateUrl: './planning-tool-nav.html',
    styleUrls: ['./planning-tool-nav.styl'],
    providers: []
})

export class PlanningToolNav implements OnInit, OnDestroy {
    @Input() childRoute;
    @Input() textKey;
    @Input() metrics: any = {};
    @Input() disabled: boolean = false;

    constructor(private router: Router, private communicator: CommunicatorService, private calcService: CalcService) { };

    ngOnInit() {
    }

    ngOnDestroy() {
    }

    onClick() {
        // if dash is disabled do nothing so return
        if (this.disabled) return;

        let dashboardPath = DASHBOARD_PATH;
        let navParam = dashboardPath + '/' + this.childRoute;
        this.router.navigate([navParam]);
    }
}
