import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, NumberFormatting as numberFormatting, TextService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';
import { StorageService } from '../../shared/storage.service';
import { INTRO_COMPLETE, HIDE_DASHBOARD_METRICS } from '../../shared/utils/utils';

const FTE_REMAINING_REF = "tlOutputEmp1FTERemaining";

@Component({
    selector: 'submit-decisions',
    templateUrl: './submit-decisions.html',
    styleUrls: ['./submit-decisions.styl']
})

export class SubmitDecisions implements OnInit, OnDestroy {
    @ViewChild('childModal', { static: true }) public childModal: CommonModalComponent;
    @Input() trigger: string;
    @Input() yearRef: string;
    @Input() disabled: boolean = false;
    public isVisible: boolean = false;
    private ref: string;
    private subscription: any;
    private remainingFTEs: number;
    public ftesLessThanZero = false;
    public initiativeLeft: number = 0;
    public SubmitDecisionsAlertText: String = '';

    constructor(private calcService: CalcService, private router: Router, private storageService: StorageService,
        private textEngineService: TextService,
        private communicator: CommunicatorService) { }

    ngOnInit() {
        if (this.trigger) {
            this.subscription = this.calcService.getObservable().subscribe(() => {
                this.checkVisible();
                this.updateFTEs();
            });
            this.checkVisible();
            this.updateFTEs();
        }
        else {
            // if year ref is not provided keep always visible
            this.isVisible = true;
        }
    }

    checkVisible() {
        this.ref = this.trigger;
        if (this.yearRef) {
            let curYear = this.calcService.getValue(this.yearRef);
            if (curYear) {
                this.ref += "_R" + curYear;
            }
        }
        this.isVisible = (Number(this.calcService.getValue(this.ref)) != 0);
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    updateFTEs() {
        if (this.yearRef) {
            this.remainingFTEs = Number(numberFormatting.unformat(this.calcService.getValueForYear(FTE_REMAINING_REF, this.yearRef)));
        } else {
            this.remainingFTEs = Number(numberFormatting.unformat(this.calcService.getValue(FTE_REMAINING_REF)));
        }

        if (this.remainingFTEs >= 0) {
            this.ftesLessThanZero = false;
        }
        else {
            this.ftesLessThanZero = true;
        }
    }

    showAlert() { 
        this.calculateInitiatives();
        this.childModal.openModal();
    }

    hideAlert(confirmed: boolean = false) {
        this.childModal.closeModal();
        // if user has confirmed submit decisions and take user to end of round feedbacks!
        if (confirmed) {
            // set year lookup range for feedbacks
            // let tlInputTeamYear = this.calcService.getValue("tlInputTeamYear");
            //this.calcService.setValue("tlInputFeedbackYear", tlInputTeamYear);

            // and update for next year
            // commenting out to test rolling into year 4 for disabling interface
            // if (tlInputTeamYear < 3) {
            //this.calcService.setValue("tlInputTeamYear", (parseInt(tlInputTeamYear)+1));
            // } else {
            // this.calcService.setValue("tlInputSimComplete", 1);
            // }
            let options = {};
            //this.router.navigate(['/password']);
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
            // mark introviewed - and persist
            this.storageService.setValue(INTRO_COMPLETE, 'false');
            // let locationNumber = this.calcService.getValue("tlInputPasswordLocation");
            // locationNumber = parseInt(locationNumber)+1;
            let locationNumber: any = this.calcService.getValue("tlInputCurrentRound");
            locationNumber = parseInt(locationNumber) * 2;
            this.calcService.setValue('tlInputPasswordLocation', locationNumber);
            let url = ['password'];
            this.router.navigate(url);
            //this.router.navigate(['/dashboard', EOR_FEEDBACK, { 'outlets': { 'year': tlInputTeamYear } }]);
        }
    }

    calculateInitiatives() {
        let curYear = this.calcService.getValue(this.yearRef);
        if(curYear == '1'){
            this.SubmitDecisionsAlertText = this.textEngineService.getText('SubmitDecisionsConfirmText');
            return;
        }
        let initiative = 0;
        for (let i = 1; i <= 2; i++) {
            let ref = 'tlInputSI0' + i + '_R' + curYear
            if (+this.calcService.getValue(ref) == 0 || +this.calcService.getValue(ref) == NaN) {
                initiative++;
            }
        }
        this.initiativeLeft = initiative;
        if(this.initiativeLeft > 0){
            this.SubmitDecisionsAlertText = this.textEngineService.getText('SubmitDecisionsAlertText').replace('{{noOfInitiatives}}', initiative.toString());
        }else{
            this.SubmitDecisionsAlertText = this.textEngineService.getText('SubmitDecisionsConfirmText');
        }
    }
}
