import { Component, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { TooltipDirective } from 'ngx-bootstrap/tooltip';
import { Utils } from '../utils/utils';

@Component({
    selector: 'my-tooltip',
    templateUrl: './tooltip.html',
    styleUrls: ['./tooltip.styl']
})

export class Tooltip implements OnInit {
    private tooltipClass: string = 'tooltipHide';
    @Input() tooltipPlacement: string = '';
    @Input() tooltipText: string = '';
    @Input() key: string = '';
    tooltipPlacement1: string = 'auto';
    @Input() keyRef: string = '';
    @Input() tabIndex?: string = '0';
    @Input() metricKey: string = '';
    @Input() rangeRef?: any;
    @Input() yearRef?: any;

    private isFocused = false;
    assertiveText: string = '';
    @ViewChild('tt', { static: true }) tooltipRef: TooltipDirective;
    @HostListener('document:keydown.escape', ['$event'])
    @HostListener('document:keydown.enter', ['$event'])
    @HostListener('document:keydown.space', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 32: // space
            case 13: // enter
                if (this.isFocused) {
                    if (this.tooltipRef.isOpen) {
                        this.tooltipRef.hide();
                    } else {
                        this.tooltipRef.show();
                    }
                    event.preventDefault();
                    event.stopPropagation();
                }
                break;
            case 27: // escape key
                if (this.isFocused) {
                    this.tooltipRef.hide();
                }
                break;
        }
    }

    constructor(private textEngineService: TextService, private calcService: CalcService, private router: Router, private utils: Utils) { }

    ngOnInit() {
        if (this.key) {
            let tooltipText = this.textEngineService.getText(this.key);

            if (tooltipText) {
                this.tooltipText = this.findAndReplaceTags(tooltipText);
            }
        }
        if (this.keyRef) {
            let tooltipText = this.calcService.getValue(this.keyRef);

            if (tooltipText) {
                this.tooltipText = tooltipText;
            }
        }
        this.metricKey = this.metricKey ? this.metricKey : this.calcService.getValueForYear(this.rangeRef, this.yearRef);
    }

    findAndReplaceTags(taggedContent) {

        if (taggedContent != undefined) {
            let startLoc = taggedContent.indexOf("{{");
            let index = 0;

            while ((startLoc != -1) && (index < 100)) {
                let endLoc = taggedContent.indexOf("}}");
                if (endLoc != -1) {
                    let lookupVal = taggedContent.substr(startLoc + 2, ((endLoc - 1) - startLoc - 1)).trim();
                    taggedContent = taggedContent.substr(0, startLoc) + this.calcService.getValue(lookupVal) + taggedContent.substr(endLoc + 2);
                }
                startLoc = taggedContent.indexOf("{{");
                index++;
            }
        }

        return taggedContent;
    }

    setFocus() {
        this.isFocused = true;
        console.log(`${this.metricKey} Info Button collapsed`);
        this.assertiveText = `${this.metricKey} Info Button collapsed`;
    }

    removeFocus() {
        this.isFocused = false;

        //Hide on blur if it was already open
        if (this.tooltipRef.isOpen) {
            this.tooltipRef.hide();
        }
    }

    showTooltip() {
        if (this.isFocused) {
            if (this.tooltipRef.isOpen) {
                this.tooltipRef.hide();
            } else {
                this.tooltipRef.show();
            }
            event.preventDefault();
            event.stopPropagation();
        }
    }

    tooltipStatus(status: string) {
        console.log(`${this.metricKey} Info Button ${status}`);
        this.assertiveText = `${this.metricKey} Info Button ${status}`;
    }
}
