import { Component, ViewChild, OnInit, OnDestroy, Input } from '@angular/core';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { ActivatedRoute, Router } from '@angular/router';
import { EOR_FEEDBACK } from '../../shared/utils/utils';

@Component({
    selector: 'marketreport',
    templateUrl: './marketreport.html',
    styleUrls: ['./marketreport.styl']
})

export class MarketReport implements OnInit, OnDestroy {
    // @ViewChild('marketModal') public childModal: ModalDirective;
    @Input() isInsidePopup = false;
    private yearRange = "tlInputCurrentRound";
    private range1Base = "tlOutputMktRptGrowSeg1";
    private range2Base = "tlOutputMktRptGrowSeg2";
    rangeref: any;
    categorylabels = { categories: ['Round 0', 'Round 1', 'Round 2', 'Round 3'] };
    serieslabels = [{ type: 'line', name: 'Seg1Name' }, { type: 'line', name: 'Seg2Name' }];
    chartOptions: any = { chart: { width: '800' }, legend: { align: 'right', layout: 'vertical', verticalAlign: 'middle', itemMarginBottom: 10 } }
    chartYAxis = {
        labels: {
            format: '{value:0%}'
        },
        title: {
            text: 'MarketReportChart1Title'
        },
        alternateGridColor: 'rgb(240, 241, 242)'
    };
    chart2YAxis = {
        labels: {
            format: '{value:0.0}'
        },
        title: { 
            text: 'MarketReportChart2Title'
        },
        alternateGridColor: 'rgb(240, 241, 242)'
    };
    currentRound: number;


    constructor(private calcService: CalcService, private router: Router,
        public activatedRoute: ActivatedRoute, private textEngineService: TextService
        ) { }

    ngOnInit() {
        this.chartYAxis.title.text = this.textEngineService.getText(this.chartYAxis.title.text)
        this.chart2YAxis.title.text = this.textEngineService.getText(this.chart2YAxis.title.text)

        this.currentRound =  +this.calcService.getValue(this.yearRange)
        let range1: any = "";
        let range2: any = "";
        let year = parseInt(this.calcService.getValue(this.yearRange));
        let maxyear = 3
        for (let i = 0; i <= maxyear; i++) {
            range1 = range1 + (this.range1Base + "R" + i + "_Y" + year + (i < maxyear ? "," : ""));
            range2 = range2 + (this.range2Base + "R" + i + "_Y" + year + (i < maxyear ? "," : ""));
        }
        let remaining = (year + 1);
        for (let j = (year + 1); j <= 3; j++) {
            // range1 = range1 + ("null");
            // range2 = range2 + ("null");
            // range3 = range3 + ("null");
            // range4 = range4 + ("null") + (j < 3 ? "," : "");
        }
        range1 = range1.split(',');
        range2 = range2.split(',');
        this.rangeref = [range1, range2];
    }

    ngOnDestroy() {

    }

    showAlert() {
        // this.childModal.show();
    }

    hideAlert() {
        // this.childModal.hide();
        // this.isVisible = false;
        this.router.navigate(['.', {outlets: {'secondary-nav': null}}], { relativeTo: this.activatedRoute.parent });
    }
}