import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ChartComponent } from './chart/chart.component';
import { HighchartsStatic } from './highchartsstatic';
import { ChartUtilsService } from './chart-utils.service';
import { TextEngineModule, CalcModule } from '@btsdigital/ngx-isomer-core';
import { HeatMapComponent } from './custom-chart/heatmap';
import { ChartDefaults } from './custom-chart/chartdefaults';
import { BasicAreaComponent } from './custom-chart/basicarea';
import { BasicLineComponent } from './custom-chart/basicline';

const chartComponents = [ChartComponent, HeatMapComponent, BasicAreaComponent, BasicLineComponent];


/**
 * Chart module for generating charts using Highcharts library
 *
 */
@NgModule({
  imports: [
    CommonModule,
    CalcModule,
    TextEngineModule,
    // ServicesModule
  ],
  declarations: chartComponents,
  exports: chartComponents,
  providers: [HighchartsStatic, ChartUtilsService, ChartDefaults]
})
export class ChartModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: ChartModule,
      providers: [HighchartsStatic, ChartUtilsService, ChartDefaults]
    };
  }
}
