import { Injectable } from '@angular/core';
import { CommunicatorService, StorageService as IsomerStorageService } from '@btsdigital/ngx-isomer-core';
import { Dictionary } from './utils/dictionary';

@Injectable()
export class StorageService {

    private dataStore: Dictionary;

    constructor(private communicatorService: CommunicatorService, private storageService: IsomerStorageService) {
        this.dataStore = {};
    }

    setValue(key: string, value: string): Promise<any> {
        return new Promise((resolve, reject) => {
            this.storageService.setValue(key, value).then((res) => {
                this.communicatorService.trigger(key, value);
                resolve(res);
            }).catch((err) => {
                reject(err);
            });
        });
    }

    getValue(key: string, forceFromPersistentStorage?: boolean): Promise<any> {
        return this.storageService.getValue(key, forceFromPersistentStorage);
    }

    setValueInDictionary(key: string, value: any): void {
        if (value) {
            this.dataStore[key] = value;
            this.communicatorService.trigger(key, value);
        }
    }

    getValueFromDictionary(key: string): any {
        return (this.dataStore[key]) ? this.dataStore[key] : null;
    }

    clearAll() {
        return this.storageService.clearAll();
    }

    clear(key: string) {
        return this.storageService.clear(key);
    }
}