import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CalcModelLoadedGuard } from '@btsdigital/ngx-isomer-core';
import { GoalSettingComponent, IntroComponent, IntroEmail, IntroOverview, IntroText, Registration } from './intro/intro';
import { ChoiceComponent } from './views/WiB-Events/choice/choice';
import { ChoiceMadeGuard } from './views/WiB-Events/choice/choice-made-guard';
import { Corporate } from './views/corporate/corporate';
import { DashboardRoute } from './views/dashboard/dashboardroute/dashboard.route';
import { EndOfRoundFeedback } from './views/endofroundfeedback/end-of-round-feedback.component';
import { YearRoute } from './views/endofroundfeedback/yearroute';
import { FeedbackComponent } from './views/WiB-Events/feedback/feedback';
import { InfoComponent } from './views/WiB-Events/info/info';
import { LogoutComponent } from './shared/logout/logout.component';
import { Operations } from './views/operations/operations';
import { PasswordComponent } from './views/password/password.component';
import { PlanningTool } from './views/planningtool/planning-tool';
import { SalesMarketing } from './views/salesmarketing/salesmarketing';
import { SplashComponent } from './shared/splash/splash.component';
// import { Tips } from './views/tips/tips';
import { MarketReport } from './views/marketreport/marketreport';
import { Reports } from './views/reports/reports';
import { AnalysisComponent } from './views/analysis/analysis.component';
import { Glossary } from './views/resources/glossary/glossary';
import { Resources } from './views/resources/resources';
import { CongratulationsComponent } from './views/congratulations/congratulations.component';
import { SwotComponent } from './intro/swot/swot.component';
import { ProductComponent } from './views/product/product.component';
import { TutorialsComponent } from './views/tutorials/tutorials.component';

const routes: Routes = [
  {
    path: 'password',
    component: PasswordComponent,
    canActivate: [CalcModelLoadedGuard],
    data: {
      noteInDebugger: 'Work based on location, might skip some. Location can be updated using Important Inputs'
    }
  },
  {
    path: 'splash',
    component: SplashComponent,
    data:{
      hideInDebugger: true
    }
  },
  {
    path: 'intro',
    component: IntroComponent,
    canActivate: [CalcModelLoadedGuard],
    children: [{
      path: 'registration',
      component: Registration
    }, {
      path: 'welcome',
      component: IntroText
    }, {
      path: 'email',
      component: IntroEmail
    }, 
    // {
    //   path: 'overview',
    //   component: IntroOverview
    // }, 
    {
      path: 'swot',
      component: SwotComponent
    }, {
      path: 'goalsetting',
      component: GoalSettingComponent
    }
  ],
  },
  {
    path: 'dashboard',
    canActivate: [CalcModelLoadedGuard],
    component: DashboardRoute,
    children: [
      {
        path: 'message/:id',
        component: InfoComponent,
        outlet: 'secondary-nav',
        data:{
          list: ['Event01_R1', 'Event01_R2', 'Event02_R3']
        }
      },
      {
        path: 'meeting/:id',
        component: ChoiceComponent,
        canActivate: [ChoiceMadeGuard],
        outlet: 'secondary-nav',
        data: {
          list: ['Event03_R1', 'Event04_R1','Event05_R1', 'Event02_R2', 'Event03_R2', 'Event04_R2', 'Event03_R3', 'Event04_R3', 'Event05_R3']
        }
      },
      {
        path: 'feedback/:id',
        component: FeedbackComponent,
        outlet: 'secondary-nav',
        data: {
          list: ['Event03_R1', 'Event04_R1','Event05_R1', 'Event02_R2', 'Event03_R2', 'Event04_R2', 'Event03_R3', 'Event04_R3', 'Event05_R3']
        }
      },
      {
        path: 'salesandmarketing',
        component: SalesMarketing
      },
      {
        path: 'product',
        component: ProductComponent
      },
      {
        path: 'operations',
        component: Operations
      },
      {
        path: 'initiatives',
        component: Corporate
      },
      // {
      //   path: 'tips',
      //   component: Tips,
      //   outlet: 'secondary-nav'
      // },
      {
        path: 'marketreport',
        component: MarketReport
      },
      {
        path: 'swot',
        component: SwotComponent,
        // outlet: 'secondary-nav'
      },
      {
        path: 'goalsetting',
        component: GoalSettingComponent,
        // outlet: 'secondary-nav'
      },
      {
        path: 'reports/:tabid',
        component: Reports,
        data: {
          list: ['incomestatement', 'competitor']
        }
      },
      {
        path: 'analysis/:tabid',
        component: AnalysisComponent,
        data: {
          list: ['bigblueanalysis', 'roundrockanalysis', 'competitor']
        }
      },
      {
        path: 'glossary',
        component: Glossary
      },
      {
        path: 'tutorials/:tabid',
        component: TutorialsComponent,
        data: {
          list: ['Tutorial01', 'Tutorial02', 'Tutorial03', 'Tutorial04', 'Tutorial05', 'Tutorial06', 'Tutorial07', 'Tutorial08', 'Tutorial09']
        }
      },
      {
        path: 'resources/:tabid',
        component: Resources,
        data: {
          list: ['guide', 'planningsteps']
        }
      },
      {
        path: 'endofroundfeedback/:year',
        component: EndOfRoundFeedback,
        data: {
          list: [1,2,3]
        }
      },
      {
        path: 'congratulations',
        component: CongratulationsComponent
      },
    ]
  },  
  {
    path: 'intro',
    component: InfoComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'endofroundfeedback/:year',
    canActivate: [CalcModelLoadedGuard],
    component: EndOfRoundFeedback,
    data: {
      list: [1,2,3]
    }
  },
  {
    path: 'logout',
    component: LogoutComponent
  },
  {
    path: '',
    redirectTo: '/splash',
    pathMatch: 'full',
    data:{
      hideInDebugger: true
    }   
  }
]

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})

export class AppRoutingModule { }
