import { Component, Input, OnInit, ElementRef } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';

@Component({
    selector: 'messages',
    templateUrl: './messages.html',
    styleUrls: ['./messages.styl'],
    providers: []
})

export class Messages implements OnInit {
    @Input() message;
    @Input() index;

    public isHidden = true;
    public isViewed = false;
    private el;


    constructor(private calcService: CalcService, private router: Router, private elRef: ElementRef) { };

    ngOnInit() {
        this.el = this.elRef.nativeElement;
        this.isHidden = (Number(this.calcService.getValue(this.message.triggerRange)) !== 1);
        this.isViewed = (Number(this.calcService.getValue("tlinputRead"+this.message.ID)) !== 0);
        this.message["icon"] = "assets/images/" + this.message["icon"];
        this.message["image"] = "assets/images/" + this.message["image"];
    }

    onSelect() {
        // [routerLink]="['/dashboard', 'messages', 'R1S1']"
        this.router.navigate(['/dashboard/message/' + this.message.ID]);
    }
}
