import { Component, ElementRef, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { CalcService, TextService, SyncService, ConnectService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { environment } from 'src/environments/environment';
import { StorageService } from '../../shared/storage.service';
import { CURRENT_YEAR, FileSaver, PASSWORD_VISIBLE_AFTERINTRO, HIDE_DASHBOARD_METRICS, RAISE_HELP, HIDE_NAVIGATION } from '../../shared/utils/utils';
import { AppConfig } from 'src/app/app.config';
import { LocationService } from 'src/app/shared/location.service';
import { Subscription } from 'rxjs';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';

declare var $: any;
const KEYCODE_NUM0 = 48;

@Component({
  selector: 'my-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.styl']
})
export class DashboardComponent implements OnInit, OnDestroy {
  public currentYear: number;
  public hideDashboardMetrics: Boolean;
  private subscription: any;
  public simComplete: any;
  private passwordToSubmit_AfterIntro = 'grainger1';
  private passwordToSubmit_AfterSubmit_R1 = 'grainger2';
  private submitPassword = '';
  private passwordVisible: any = 0;
  private showPasswordError = false;
  public showMenu: boolean = false;
  private isFollower = 0;
  public userIsForeman = true;
  setTime: any;
  helpActive: boolean = false;
  activeTab: string = '';
  HELP_REF = 'tlInputHelp';
  isIntro: boolean = false;
  routingSubscription: Subscription;
  @ViewChild('childModal', {static: false}) public childModal: CommonModalComponent;
  // @ViewChild('childModal', { static: true }) public childModal: ModalDirective;
  @ViewChild('menuContainer', { static: true }) public menuContainer: ElementRef;
  @ViewChild('menuOptionsContainer', { static: true }) public menuOptionsContainer: ElementRef;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEYCODE_NUM0) {
      this.resetSim();
    }
  }

  @HostListener('document:keydown.arrowup', ['$event'])
  @HostListener('document:keydown.arrowdown', ['$event'])
  @HostListener('document:keydown.arrowleft', ['$event'])
  @HostListener('document:keydown.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        $('.menu-options-button:focus').next().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
      case 37:
      case 38:
        $('.menu-options-button:focus').prev().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
    }
  }
  constructor(private calcService: CalcService, private textEngineService: TextService, private fileSaver: FileSaver, 
              private storageService: StorageService, private syncService: SyncService, private connectservice: ConnectService,
              private router: Router, private communicator: CommunicatorService,  private appConfig: AppConfig,
              private locationService: LocationService
              ) { }

  ngOnInit() {
    this.isIntro = this.router.url.indexOf('intro') > -1 ? true : false;
    this.routingSubscription = this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.isIntro = this.router.url.indexOf('intro') > -1 ? true : false;
      }
    });
    
    this.getCurrentYear();
    this.checkSimComplete();
    
    if (this.appConfig.configuration.connectToPulse) {
      this.checkIfFollower();
      setTimeout(() => {
        this.checkIfFollower();
      }, 2000);
    }
    this.subscription = this.calcService.getObservable().subscribe(() => {
      this.getCurrentYear();
      this.checkSimComplete();

      if (this.appConfig.configuration.connectToPulse) {
        this.checkIfFollower();
      }
    });
    // this.calcService.getApi();
    this.passwordVisible = this.calcService.getValue(PASSWORD_VISIBLE_AFTERINTRO);
    if (this.appConfig.configuration.connectToPulse) {
      this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
    }

    this.communicator.getEmitter(HIDE_DASHBOARD_METRICS).subscribe(res => {
      this.hideDashboardMetrics = ((res + '') === 'true') ? true : false;
    });
  }

  getCurrentYear() {
    this.currentYear = Number(this.calcService.getValue('tlInputCurrentRound'));
    this.storageService.setValue(CURRENT_YEAR, this.currentYear + '');
  }

  checkSimComplete() {
    this.simComplete = Number(this.calcService.getValue('tlInputSimComplete'));
    // if (this.simComplete) {
    //   this.router.navigate(['dashboard/congratulations'])
    // }
  }

  resetSim() {
    let confirmText = this.textEngineService.getText("ResetSim");
    let confirmResponse = confirm(confirmText);
    if (confirmResponse) {
      this.storageService.clearAll().then(() => {
        window.location.reload();
      });
    }
    // localStorage.clear();
    // window.location.reload();
  }

  showAlert() {
    this.childModal.openModal();
  }

  hideAlert(confirmed: boolean = false) {
    this.childModal.closeModal();
  }
  exportData() {
    let modelState = this.calcService.exportData('^tl(In|Out)put.+$');
    var teamNo = this.calcService.getValue("tlInputTeamNumber");
    var teamYear = this.calcService.getValue("tlInputCurrentRound");
    var fileNamePrefix = "WIB_Isomer";
    var fileName = fileNamePrefix + teamNo + "Y" + teamYear + ".csv";
    var delim = ',';
    this.fileSaver.fileSaveAs(fileName, modelState, delim);
  }

  checkIfFollower() {
    let val = this.locationService.getFollowerStatus();// returns 1 for follower and 0 for leader
    if (this.isFollower != val) {
        this.isFollower = val
        console.log("Leader/Follower Status: " + this.isFollower);
        if (this.isFollower == 1) {
            if (!document.body.classList.contains("follower")) {
                document.body.classList.add("follower");
            }
        } else {
            if (document.body.classList.contains("follower")) {
                document.body.classList.remove("follower");
            }
        }
    }
  }
  
  openMenu($event) {
    this.showMenu = !this.showMenu;
    $event.preventDefault();
    $event.stopPropagation();
  }

  closeMenu() {
    this.showMenu = false;
  }

  getHelp() {
    this.closeMenu();
    this.communicator.trigger(RAISE_HELP)
  }

  ngOnDestroy() {
    this.routingSubscription.unsubscribe();
    this.subscription.unsubscribe();
  }
}
