import { Component, Input, OnInit } from '@angular/core';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import * as _ from 'lodash';

@Component({
    selector: 'feedbacktextandtable',
    templateUrl: './feedbacktextandtable.html',
    styleUrls: ['./feedbacktextandtable.styl'],
    providers: []
})

export class FeedbackTextAndTable implements OnInit {
    @Input() index;
    @Input() feedback = {};

    public title = "";
    public narrative = "";
    private narrativeArray = [];

    private isHidden = true;
    private isViewed = false;

    tableData = [];

    rangeref: any;
    categorylabels: any;
    subcategorylabels: any;
    serieslabels: any;
    tabletitle: any;

    localRangeRefArray: any;
    localCategoryLabelsArray: any;
    localSubCategoryLabelsArray: any;
    localSeriesLabelsArray: any;

    arrCat = []
    arrSubCat = []
    arrSeries = []
    arrRanges = []
    catColSpan = 1;

    constructor(
        private calcService: CalcService, 
        private router: Router,
        private textService: TextService
    ) { };

    ngOnInit() {
        this.getTableData()
        let narrativeChunk:string = "";

        this.title = this.feedback["name"];

        let index:number = 1;
        narrativeChunk = this.feedback["narrative"+index];

        while ((narrativeChunk != undefined) && (index < 100)) {
            let conditional:any = true;
            let conditionalChunk = this.feedback["condition"+index];

            if (conditionalChunk != undefined) {
                conditional = this.calcService.getValue(conditionalChunk);
            }
            if (conditional && conditional != "0") {
                this.narrative += narrativeChunk;
            }

            index++;
            narrativeChunk = this.feedback["narrative"+index];
        }

        if (this.narrative != undefined) {
            let startLoc = this.narrative.indexOf("{{");
            index = 0;

            while ((startLoc != -1) && (index < 100)) {
                let endLoc = this.narrative.indexOf("}}");
                if (endLoc != -1) {
                    let lookupVal = this.narrative.substr(startLoc+2, ((endLoc-1)-startLoc-1)).trim();
                    this.narrative = this.narrative.substr(0, startLoc) + this.calcService.getValue(lookupVal) + this.narrative.substr(endLoc+2);
                }
                startLoc = this.narrative.indexOf("{{");
                index++;
            }
        }
    }

    getTableData(){
        this.tableData = [];
        let index = 1;
        let tableRanges = this.feedback["table"+index+"ranges"];
        if(tableRanges){
            // Multiple tables
            while(tableRanges){
                this.rangeref = this.feedback["table"+index+"ranges"];
                this.categorylabels = this.feedback["table"+index+"categories"];
                this.subcategorylabels = this.feedback["table"+index+"subcategories"];
                this.serieslabels = this.feedback["table"+index+"series"];
                this.tabletitle = this.feedback["table"+index+"title"];
                this.processComponentInputs();
    
                this.tableData.push({
                    rangeref: [...this.arrRanges],
                    series: [...this.arrSeries],
                    categories: [...this.arrCat],
                    subCat: [...this.arrSubCat],
                    title: this.tabletitle
                })

                index++
                tableRanges = this.feedback["table"+index+"ranges"];
            }
        }else{
            //Single Table
            this.rangeref = this.feedback["tableranges"];
            this.categorylabels = this.feedback["tablecategories"];
            this.subcategorylabels = this.feedback["tablesubcategories"];
            this.serieslabels = this.feedback["tableseries"];
            this.tabletitle = this.feedback["tabletitle"];
    
            this.processComponentInputs();

            this.tableData.push({
                rangeref: [...this.arrRanges],
                series: [...this.arrSeries],
                categories: [...this.arrCat],
                subCat: [...this.arrSubCat],
                title: this.tabletitle
            })

        }
    }

    processComponentInputs() {
        let localRangeRef = this.rangeref,
            localCategoryLabels = this.categorylabels,
            localSubCategoryLabels = this.subcategorylabels,
            localSeriesLabels = this.serieslabels;

        if (localRangeRef != undefined && localRangeRef != null) {
            localRangeRef = this.rangeref.toString();
            localRangeRef = localRangeRef.replace(/\[|\]/g, '');
            localRangeRef = localRangeRef.replace(/ /g, '');
            this.localRangeRefArray = localRangeRef.split(",");
        }

        if (localCategoryLabels != undefined && localCategoryLabels != null) {
            localCategoryLabels = this.categorylabels.toString();
            localCategoryLabels = localCategoryLabels.replace(/\[|\]/g, '');
            // localCategoryLabels = localCategoryLabels.replace(/ /g, '');
            this.localCategoryLabelsArray = localCategoryLabels.split(",").map(function(item) {
                return item.trim();
            });;

            for (let i = 0; i < this.localCategoryLabelsArray.length; i++) {
                let tmpLabel = this.textService.getText(this.localCategoryLabelsArray[i]);
                if (tmpLabel != undefined) {
                    this.localCategoryLabelsArray[i] = tmpLabel;
                }
            }
        }

        if (localSubCategoryLabels != undefined && localSubCategoryLabels != null) {
            localSubCategoryLabels = this.subcategorylabels.toString();
            localSubCategoryLabels = localSubCategoryLabels.replace(/\[|\]/g, '');
            // localSubCategoryLabels = localSubCategoryLabels.replace(/ /g, '');
            this.localSubCategoryLabelsArray = localSubCategoryLabels.split(",").map(function(item) {
                return item.trim();
            });;

            for (let i = 0; i < this.localSubCategoryLabelsArray.length; i++) {
                let tmpLabel = this.textService.getText(this.localSubCategoryLabelsArray[i]);
                if (tmpLabel != undefined) {
                    this.localSubCategoryLabelsArray[i] = tmpLabel;
                }
            }
        }

        if (localSeriesLabels != undefined && localSeriesLabels != null) {
            localSeriesLabels = this.serieslabels.toString();
            localSeriesLabels = localSeriesLabels.replace(/\[|\]/g, '');
            // localSeriesLabels = localSeriesLabels.replace(/ /g, '');
            this.localSeriesLabelsArray = localSeriesLabels.split(",").map(function(item) {
                return item.trim();
            });;

            for (let i = 0; i < this.localSeriesLabelsArray.length; i++) {
                let tmpLabel = this.textService.getText(this.localSeriesLabelsArray[i]);
                if (tmpLabel != undefined) {
                    this.localSeriesLabelsArray[i] = tmpLabel;
                }
            }
        }

        this.getTableCategories();
        this.getTableSubCategories();
        this.getTableRangeRef();
        this.getTableSeries();
    }

    getTableRangeRef() {
        this.arrRanges = [];
        if(this.localSubCategoryLabelsArray && this.localSubCategoryLabelsArray.length > 0) {
            this.arrRanges =_.chunk(this.localRangeRefArray, this.localSubCategoryLabelsArray.length);
        }else{
            this.arrRanges =_.chunk(this.localRangeRefArray, this.localCategoryLabelsArray.length);
        }
    }

    getTableSeries() {
        this.arrSeries = [...this.localSeriesLabelsArray];
    }

    getTableCategories() {
        this.arrCat = [this.tabletitle, ...this.localCategoryLabelsArray]
    }

    getTableSubCategories() {
        this.arrSubCat = []
        if(this.localSubCategoryLabelsArray && this.localSubCategoryLabelsArray.length){
            this.catColSpan = this.localSubCategoryLabelsArray.length / this.localCategoryLabelsArray.length 
            this.arrSubCat = ['', ...this.localSubCategoryLabelsArray]
        }
    }

}
