import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ImportExportComponent } from './import-export.component';
import { ImportFileComponent } from './importfile/importfile.component';
import { CsvFileReaderService } from './import-export-services/csvfilereader';
import { FileSaver } from './import-export-services/filesaver';
import { ModalModule } from 'ngx-bootstrap/modal';
import { SharedModule } from '../shared/shared.module';

@NgModule({
  imports: [
    CommonModule,
    ModalModule.forRoot(),
    SharedModule
  ],
  declarations: [ImportExportComponent, ImportFileComponent],
  exports: [ImportExportComponent],
  providers: [CsvFileReaderService,FileSaver]
})
export class ImportExportModule { }
