import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, SyncService, TextService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from 'src/app/shared/location.service';
import { environment } from 'src/environments/environment';
import * as Collections from 'typescript-collections';
import { TextHelperService } from '../../../shared/text-service.extended';
import { Utils } from '../../../shared/utils/utils';
import { Chooser } from './chooser';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'single-select',
    templateUrl: './single-select.html',
    styleUrls: ['./single-select.styl']
})

export class SingleSelect implements OnInit, OnDestroy, Chooser {
    el: HTMLElement;
    title = "Info component !!!";
    choices = [];
    isViewed = false;
    answer: number = -1;
    radioModel: number = -1;
    isClosing: boolean = false;
    uncheckable: boolean = false;
    @Input() id: string;
    @Input() scene: Collections.Dictionary<string, string>;
    readyToSubmit = false;
    image: string = "";
    tutorialData;
    tutorialName: string;

    userIsForeman: boolean = true;
    @Input() disabled: boolean = false;

    constructor(private textengineService: TextHelperService, private calcService: CalcService,
        private syncService: SyncService,
        private locationService: LocationService,
        private router: Router, private utils: Utils, private appConfig: AppConfig,
        private textService: TextService) { };

    ngOnInit() {
        // this.el = this.elRef.nativeElement;
        // this.isClosing = false;
        // //this.id = +this.route.params['id'];
        // //debugger;
        // this.route.params.forEach(params => this.id = params['id']);
        // console.log("loading Message / meeting id: " + this.id);
        // this.id = (this.id) ? this.id : 1;
        // this.scene = this.textengineService.getScene(this.id);
        var curVal = this.calcService.getValue("tlinputDec" + this.scene.getValue("ID"));

        if (curVal) {
            this.answer = Number(curVal);
        }

        this.title += " " + this.id;

        // set choices array
        //var localObj = this.scene;
        var localChoices = [];

        localChoices = this.textengineService.getSingleSelectChoices(this.id);
        // loop while we still have an 'alt' value

        this.choices = localChoices;
        this.image = "assets/images/" + this.scene.getValue("image");
        // console.log ("choices");
        // console.log (this.choices);

        // check if there is tutorial present
        let tutorialID = this.scene.getValue("tutorial");
        if (tutorialID) {
            let tutorialScene = this.textService.getScene(tutorialID)
            this.tutorialData = this.utils.singleDictToObj(tutorialScene);
        }
        
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
    }

    onSelect($event) {
        // console.log("ngModel Changed", this.answer);
        if (!this.disabled) {
            this.readyToSubmit = true;
        }
    }

    submit() {
        this.calcService.setValue("tlinputDec" + this.scene.getValue("ID"), this.answer);
        this.calcService.setValue("tlinputRead" + this.scene.getValue("ID"), 1);
        // console.log("Choice made --- " + window.performance.now());
    }

    ngOnDestroy() {
        this.isClosing = false;
    }

    isReadyToSubmit(): boolean {
        return (this.readyToSubmit !== undefined) ? this.readyToSubmit : false;
    }
    onSubmit() {
        // submit decision on the chooser component
        if (!this.userIsForeman) {
            return
        }
        this.submit();
        // now navigate to feedback
        this.locationService.setLocation(this.utils.generateFeedbackUrl(this.id), 1);
        this.router.navigate(this.utils.generateFeedbackUrl(this.id));
    }
}
