import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { MESSAGE_LINK_ADDED_EVENT } from '../link/link.component';
import { CommunicatorService, CalcService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';

@Component({
  selector: 'ism-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.styl']
})
export class NotificationComponent implements OnInit {
    public notificationTitle = "";
    public notificationType = "";
    public notificationVisible = false;
    public notificationImage = false;
    private subscription: Subscription;

    constructor(
        private communicator: CommunicatorService,
        private router: Router,
        private calcService: CalcService
    ) { }

    ngOnInit() {
        this.subscription = this.communicator.getEmitter(MESSAGE_LINK_ADDED_EVENT).subscribe((data) => {
            let isViewed = (Number(this.calcService.getValue("tlinputRead" + data.ID)) !== 0);
            this.notificationVisible = !isViewed;
            if (data) {
                if (data.PageType == "SingleSelect") {
                    this.notificationType = "New Meeting"
                    // this.notificationType = this.textengineService.getText('Meeting');
                }
                else {
                    this.notificationType = "New Message"
                    // this.notificationType = this.textengineService.getText('Meeting');
                }
                this.notificationTitle = data.name;
                this.notificationImage = data.icon;
            }
        });

    }

    navigate() {
        this.notificationVisible = false;
        if(this.subscription){
            this.subscription.unsubscribe();
        }
        this.router.navigateByUrl('/dashboard');
    }

    ngOnDestroy(){
        if(this.subscription){
            this.subscription.unsubscribe();
        }
    }

}
