import { Component, OnInit, Input, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'isma-modal-image',
  templateUrl: './modal-image.component.html',
  styleUrls: ['./modal-image.component.styl']
})
export class ModalImageComponent implements OnInit {
  @Input() path: string = '';
  @ViewChild('largeImage',{static: false}) largeImageRef: ElementRef;
  @ViewChild('modal',{static: false}) ModalRef: ElementRef;
  private modal: ElementRef;
  isActive: boolean = false;

  constructor(private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
  }

  expandImage(){
    this.isActive = true;

    var body = document.getElementsByTagName("body")[0];
    this.modal = this.ModalRef;
    if (this.modal) {
        body.appendChild(this.modal.nativeElement);
    }
  }

  collapseImage(){
    let self = this;
    this.largeImageRef.nativeElement.className += " out"
    setTimeout(function() {
      self.isActive = false;
      self.largeImageRef.nativeElement.className = "largeImage";
      self.cdRef.detectChanges();

      self.removeFromBody();
    }, 400);
  }

  removeFromBody(){
    var body = document.getElementsByTagName("body")[0];
    if (this.modal) {
        body.removeChild(this.modal.nativeElement);
    }
  }

}
