import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { DD_MESSAGE, DD_STATUS, DD_TIMER } from '../utils/utils';

@Component({
  selector: 'ism-notification-message',
  templateUrl: './notification-message.component.html',
  styleUrls: ['./notification-message.component.styl']
})
export class NotificationMessageComponent implements OnInit, OnDestroy {
  message: string = '';
  status: number = 0;
  timer: number = 0;
  showMsg: boolean = false;
  protected observable: Subscription;

  @Input() inputClass? = 'primary'

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.showNotification();
    this.observable = this.calcService.getObservable().subscribe((result) => {
      this.showNotification();
    });

  }

  showNotification() {
    this.message = this.calcService.getValue(DD_MESSAGE);
    this.status = +this.calcService.getValue(DD_STATUS);
    this.timer = +this.calcService.getValue(DD_TIMER);
    this.showMsg = this.status == 1 ? true : false;

    if (this.timer) {
      setTimeout(() => {
        this.onClose();
      }, this.timer*1000)
    }
  }

  onClose() {
    this.calcService.setValue('DD_Status', 0);
    this.showMsg = false;

  }

  ngOnDestroy() {
    if (this.observable) {
      this.observable.unsubscribe();
    }
  }

}
