import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';


@Component({
    selector: 'isma-calc-checkbox',
    templateUrl: './calc-checkbox.component.html',
    styleUrls: ['./calc-checkbox.component.styl'],
    providers: []
})

export class CalcCheckboxComponent implements OnInit, OnDestroy {
    @Input() ref: string = "";
    @Input() type: string = "checkbox";
    public value: boolean;
    private tempValue: boolean = false;
    private isDirty: boolean = false;
    private subscription: any;

    constructor(private calcService: CalcService) {};

    ngOnInit() {
        //this.value = this.calcService.getValueForYear(this.ref, this.yearRef);
        this.subscription = this.calcService.getObservable().subscribe(() => {
            this.onModelChange();
        });
        this.onModelChange();
    }

    saveDataToModel() {
        let value: boolean;
        value = this.tempValue;
        this.calcService.setValue(this.ref, value);
        this.value = this.calcService.getValue(this.ref)=='1';
    }

    toggleValue() {
        this.tempValue = !this.value;
        this.saveDataToModel();
    }

    onModelChange() {
        this.value = this.calcService.getValue(this.ref)=='1';
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }
    getvalue(){
        return this.value;
    }

}
