import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { StorageService } from '../../shared/storage.service';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { CURRENT_YEAR } from 'src/app/shared/utils/utils';
import { environment } from 'src/environments/environment';

/**
 * NavBar component renders the navigation bar based on the provided input
 * Navigation bar is rendered using ism-nav-bar component of ngx-isomer-core
 *
 */
@Component({
    selector: 'ism-nav-bar',
    templateUrl: './nav-bar.component.html',
    styleUrls: ['./nav-bar.component.styl']
})
export class NavBarComponent implements OnInit {
    private subscription: Subscription;
    public currentYear: number;
    public simComplete: any;
    public decisionsEnabled = true;

    constructor(
        private router: Router,
        private storage: StorageService,
        private calcService: CalcService,
        private storageService: StorageService,
        private communicator: CommunicatorService
    ) { }

    ngOnInit() {
        this.getCurrentYear();
        this.checkSimComplete();
        this.checkIfDecisionsEnabled()
        
        this.subscription = this.calcService.getObservable().subscribe(() => {
            this.getCurrentYear();
            this.checkSimComplete();
            this.checkIfDecisionsEnabled()
          });
    }

    getCurrentYear() {
        this.currentYear = Number(this.calcService.getValue('tlInputCurrentRound'));
        this.storageService.setValue(CURRENT_YEAR, this.currentYear + '');
    }

    checkSimComplete() {
        this.simComplete = Number(this.calcService.getValue('tlInputSimComplete')) == 1;
    }

    checkIfDecisionsEnabled(){
        if(this.currentYear == 1){
            this.decisionsEnabled = Number(this.calcService.getValue('tlinputReadEvent01_R1')) == 1;
        }else if(this.currentYear == 2){
            this.decisionsEnabled = Number(this.calcService.getValue('tlinputReadEvent01_R2')) == 1;
        }else if(this.currentYear == 3){
            this.decisionsEnabled = Number(this.calcService.getValue('tlinputReadEvent02_R3')) == 1;
        }
    }

    ngOnDestroy(){
        if(this.subscription){
            this.subscription.unsubscribe()
        }
    }
}
