import { Component, Input, OnInit, OnDestroy, ElementRef, ViewEncapsulation, Output, HostListener, SimpleChanges, OnChanges } from '@angular/core';
import { CalcService, TextService, NumberFormatting, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import * as noUiSlider from 'nouislider';


@Component({
    selector: 'isma-calc-slider',
    templateUrl: './calc-slider.component.html',
    styleUrls: ['./calc-slider.component.styl'],
    encapsulation: ViewEncapsulation.Emulated
})

export class CalcSliderComponent implements OnInit, OnChanges, OnDestroy{
    @Input() ref: string = "";
    @Input() yearRef: string;
    @Input() step: number;
    @Input() min: number;
    @Input() max: number;
    @Input() showButtons: boolean = false;
    @Input() onChange: any;
    @Input() handleValueRef: string;
    @Input() disabled: boolean = false;
    @Input() xapiTrackingOnPulse: boolean = false;
    @Input() xapiTrackingOnLoad: boolean = null;
    @Input() targetEleRef?: any = null;
    @Input() isDisabled: boolean = false;
    @Input() tabIndex?: string = '0';
    private value: string | number;
    private tempValue: string;
    private year: string;
    private isDirty: boolean = false;
    private el: any;
    private subscription: any;
    private handleValue: string;
    assertiveText: string = '';
    targetElement: any = null;
    private currSliderValue: string | number;

    @HostListener('document:keyup', ['$event'])
    onKeyUp(ev: KeyboardEvent) {
        ev.preventDefault();
        ev.stopPropagation();
        if (ev.keyCode === 9 && this.el.querySelector(`#${this.ref}`).querySelector('.noUi-handle.noUi-handle-lower').matches(':focus')) {
            if (this.targetEleRef instanceof Array) {
                let text = '';
                this.targetEleRef.forEach((targets) => {
                    targets.forEach(target => {
                        let value = this.calcService.getValueForYear(target.ref, this.yearRef, true);
                        if (target.format) {
                            value = NumberFormatting.format(value, target.format);
                            text += value + ' ';
                        } else {
                            text += value + ' ';
                        }
                    });
                });
                this.assertiveText = `Focus is on slider. Current value of slider is ${text}. Press up or right arrow key to slide to the right, down or left arrow key to slide to the left`;
            } else {
                this.targetElement = this.calcService.getValueForYear(this.targetEleRef, this.yearRef, false);
                this.assertiveText = `Focus is on slider. Current value of ${this.targetElement ? this.targetElement : 'slider'} is ${this.currSliderValue}. Press up or right arrow key to slide to the right, down or left arrow key to slide to the left`;
            }
            this.el.querySelector(`#${this.ref}`).querySelector('.noUi-handle.noUi-handle-lower')
                .addEventListener('focusout', this.clearAssertiveText);
        }
    }

    constructor(private calcService: CalcService, private elRef: ElementRef, private isomerXapiService: IsomerXapiService) {
        this.el = elRef.nativeElement;
    }

    ngOnInit() {

        this.value = this.calcService.getValue(this.ref);
        this.value = NumberFormatting.unformat(this.value);
        let slider = this.el.querySelector('.calc-slider');
        // let pips: Array<HTMLElement> = this.el.getElementsByClassName('pip-steps');
        let opt: any = {
            start: [Number(this.value)],
            range: {
                'min': Number(this.min),
                'max': Number(this.max)
            },
            step: (this.step) ? Number(this.step) : undefined,
            behavior: 'tap-drag',
            pips: {
                mode: 'steps',
                density: 1
            }
        }
        if (slider) {
            noUiSlider.create(slider, opt);
            this.currSliderValue = slider.noUiSlider.get();
            slider.getElementsByClassName('noUi-handle noUi-handle-lower')[0].setAttribute('aria-label', this.currSliderValue);
            this.bindEvents(slider);
            this.updateValues();
        }

        if(this.isDisabled && slider){
            slider.setAttribute('disabled', true)
        }else{
            slider.removeAttribute('disabled');
        }

        this.subscription = this.calcService.getObservable().subscribe(() => {
            this.updateValues();
        });

        // track data on load
        this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref),
        this.calcService.getRefNameByYearRef(this.ref, this.yearRef), String(this.value),
        this.yearRef ? this.calcService.getValue(this.yearRef) : this.calcService.getRoundValue(), 'calc-slider',
        this.xapiTrackingOnLoad, this.xapiTrackingOnPulse);

    }

    ngOnChanges(change: SimpleChanges) {
        let handle = this.el.querySelector('.noUi-handle');
        if (handle && change.tabIndex) {
            handle.setAttribute('tabindex', change.tabIndex.currentValue);
        }
    }

    updateValues() {
        this.handleValue = this.calcService.getValue(this.handleValueRef, true);
        this.updateHandleValue();
    }

    bindEvents(slider: any) {
        if (slider && slider.noUiSlider) {
            slider.noUiSlider.on('change', () => {
                const curVal = slider.noUiSlider.get();
                if (this.targetEleRef instanceof Array) {
                    let text = '';
                    this.saveDataToModel(curVal);
                    setTimeout(() => {
                        this.targetEleRef.forEach((targets) => {
                            targets.forEach(target => {
                                let value = this.calcService.getValueForYear(target.ref, this.yearRef, false);
                                if (target.format) {
                                    value = NumberFormatting.format(value, target.format);
                                    text += value + ' ';
                                } else {
                                    text += value + ' ';
                                }
                            });
                        });
                        this.assertiveText = text;
                    }, 500);
                } else {
                    this.assertiveText = `Current value of ${this.targetElement ? this.targetElement : 'slider'} is ${curVal}.`;
                    this.saveDataToModel(curVal);
                }
                slider.getElementsByClassName('noUi-handle noUi-handle-lower')[0].setAttribute('aria-label', curVal);

                if (this.onChange) {
                    this.onChange(this.el);
                }
            });
        }
    }

    updateHandleValue() {
        if (this.handleValueRef) {
            if (this.handleValue !== null) {
                // this.el.querySelector('.noUi-handle').innerHTML = this.handleValue + "%";
                let sliderValue = this.calcService.getValue(this.ref, true)
                if (sliderValue != '' && sliderValue != null) {
                    this.el.querySelector('.calc-slider').noUiSlider.set(sliderValue);
                }
            }
        }

    }

    saveDataToModel(val: any) {
        console.log("save to model called");
        // this.isDirty = false;
        // this.calcService.setValue(this.ref, val);
        this.calcService.setValueForYear(this.ref, val, this.yearRef, 'calc-slider', this.xapiTrackingOnPulse);
    }


    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    clearAssertiveText() {
        this.targetElement = null;
        this.assertiveText = '';
    }

}
