import { Component, OnDestroy, OnInit, HostListener } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { CalcService, TextService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
// import { Observable } from 'rxjs';
import { Subscription } from 'rxjs';
import { LocationService } from '../../shared/location.service';
import { StorageService } from '../../shared/storage.service';
import * as _ from 'lodash';
import { AppConfig } from 'src/app/app.config';
import * as $ from 'jquery';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';

@Component({
    selector: 'tutorials',
    templateUrl: './tutorials.component.html',
    styleUrls: ['./tutorials.component.styl']
})

export class TutorialsComponent implements OnInit, OnDestroy {
    private subscription: Subscription;
    public activeTab: string;
    navIndex: any = "0"
    tutorialsdata = [];

    @HostListener('document:keydown.arrowup', ['$event'])
    @HostListener('document:keydown.arrowdown', ['$event'])
    @HostListener('document:keydown.arrowleft', ['$event'])
    @HostListener('document:keydown.arrowright', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 39:
            case 40:
                $('.nav-item:focus').next().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
            case 37:
            case 38:
                $('.nav-item:focus').prev().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
        }
    }

    constructor(
        private textengineservice: TextHelperService,
        activatedRoute: ActivatedRoute,
        private storageService: StorageService,
        private locationService: LocationService,
        private router: Router,
        private appConfig: AppConfig,
        private communicator: CommunicatorService,
        private utils: Utils
    ) {

        this.subscription = activatedRoute.params.subscribe(
            (param: any) => {
                let activeTab = (param['tabid']) ? param['tabid'] : null;
                this.activeTab = activeTab;
            });
    }

    ngOnInit() {
        let data = this.textengineservice.getPageType('Tutorial')
        this.tutorialsdata = this.utils.dictToObj(data);
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }


    setactiveTab(e, _subTab: string): any {
        if (_subTab === null) return;

        const url = '/dashboard/tutorials/' + _subTab;
        this.locationService.setLocation(url, 0);
        this.router.navigate([url]);
    }
}
