import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { Dictionary } from 'src/app/shared/utils/utils';

@Component({
    selector: 'glossary',
    templateUrl: './glossary.html',
    styleUrls: ['./glossary.styl']
})

export class Glossary implements OnInit, OnDestroy {

    public alphabets: any = [];
    public activeIndex: number = 0;
    public glossaryContent: Dictionary;
    private glossaryContentPromise: Promise<any>;
    public searchAlphabet: string = '';
    public glossaryObj: any = [];
    assertiveText: string = '';

    constructor(private http: HttpClient, private calcService: CalcService, private textengineservice: TextService, private router: Router) { }

    ngOnInit() {
        for (let i = 65; i <= 90; i++) {
            this.alphabets.push(String.fromCharCode(i));
        }

        this.loadTextContent();

        this.glossaryContent = this.glossaryObj;

        return this.glossaryContent;
    }

    // Loads glossary content from content doc ranges
    loadTextContent() {

        let index = 1
        for (let x = 65; x <= 90; x++) {
            let alpha = String.fromCharCode(x);
            let alphaBlock = [];
            let alphaCount = 1
            let glossaryTitle = this.textengineservice.getText(alpha + index + "GlossaryTitle");
            let glossaryDesc = this.textengineservice.getText(alpha + index + "GlossaryDescription");

            while ((glossaryTitle != undefined) && (alphaCount < 100)) {
                alphaBlock.push ({"title" : glossaryTitle, "description" : glossaryDesc});
                alphaCount++;
                index++;
                glossaryTitle = this.textengineservice.getText(alpha + index + "GlossaryTitle");
                glossaryDesc = this.textengineservice.getText(alpha + index + "GlossaryDescription");
            }
            if (alphaCount > 1) {
                this.glossaryObj.push({"alphabet" : alpha, "glossary" : alphaBlock});
            }
        }
    }

    ngOnDestroy() {

    }

    scrollGlossaryList(event, index: string) {
        this.activeIndex = parseInt(index);
        let toElement = document.getElementById(event.currentTarget.getAttribute('id').replace('#', ''));

        if (toElement) {
            this.scrollTo(document.getElementsByClassName('glossary-list')[0], toElement.offsetTop - 132, 400);
        }
        this.assertiveText = index + (event.target.classList.contains('selected') ? 'selected' : 'not selected');
    }

    scrollTo(element, to, duration) {
        let self = this;
        if (duration <= 0) return;
        var difference = to - element.scrollTop;
        var perTick = difference / duration * 10;

        setTimeout(function () {
            element.scrollTop = element.scrollTop + perTick;
            if (element.scrollTop === to) return;
            self.scrollTo(element, to, duration - 10);
        }, 10);
    }

    searchGlossary(event) {
        this.searchAlphabet = event.currentTarget.value;
    }
}
