import { Component, OnDestroy, OnInit, AfterViewInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, SyncService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import * as Collections from 'typescript-collections';
import { TextHelperService } from '../../shared/text-service.extended';
import { AppConfig } from 'src/app/app.config';
import { LocationService } from 'src/app/shared/location.service';

@Component({
    selector: 'corporate',
    templateUrl: './corporate.html',
    styleUrls: ['./corporate.styl']
})

export class Corporate implements OnInit, OnDestroy, AfterViewInit {
    initiatives: Collections.Dictionary<string, any>;
    initiativesArray = [];
    currentYear = 0;
    maxChecks = 0;
    totalChecked = 0;
    subscription: Subscription;
    userIsForeman = true;

    hideCardAnimation = true;

    isFiltered = false
    filters:Array<string> = [];
    hasCategories = false;
    categories:Array<string> = [];

    constructor(
        private calcService: CalcService,
        private textHelper: TextHelperService, 
        private syncService: SyncService, 
        private communicator: CommunicatorService,
        private appConfig: AppConfig,
        private locationService: LocationService,
        private router: Router
    ) { }

    ngAfterViewInit(){
        setTimeout(() => {
            this.hideCardAnimation = false;
        }, 1000);
    }

    ngOnInit() {
        this.initializeInitiativeData();

        this.locationService.setLocation(this.router.url, 0);

        this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
            this.onModelChange();
        });
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }

    initializeInitiativeData(){
        //get data from content file
        this.initiatives = this.textHelper.getInitiatives();
        //process data to create the array locally
        this.initiatives.keys()
            .forEach((key) => {
                const _initiative = this.initiatives.getValue(key);
                const localScene = [];
                _initiative.keys()
                    .forEach((_key) => {
                        localScene[_key] = _initiative.getValue(_key);
                    })
                    localScene["isChecked"] = 0;
                    localScene["isSelected"] = 0;
                    localScene["isEnabled"] = true;
                    //Add in category array if unique
                    if(localScene['category'] && this.categories.indexOf(localScene['category']) == -1){
                        this.categories.push(localScene['category'].trim())
                    }
                this.initiativesArray.push(localScene);
            });

        this.hasCategories = this.categories.length > 0;

        this.currentYear = Number(this.calcService.getValue("tlInputCurrentRound"));
        let loopYear = 1;

        //Updated checked state of by processing all values from the model
        if ((this.currentYear >= 1) && (this.currentYear < 100)) {
            // assigning all model data to local structures
            for (let y = 1; y <= this.currentYear; y++) {
                this.maxChecks = 0;
                let currentInit = "tlInputSI0" + (this.maxChecks + 1) + "_R" + y;
                let initiativesAvail = (this.calcService.getValue(currentInit) != undefined);
                while (initiativesAvail && (this.maxChecks < 10)) {
                    this.maxChecks++;
                    let currentIndex = Number(this.calcService.getValue(currentInit).replace('SI', '')) - 1;
                    if (currentIndex >= 0) {
                        this.initiativesArray[currentIndex].isChecked = y;
                        if (y < this.currentYear) {
                            this.initiativesArray[currentIndex].isEnabled = false;
                        }
                    }
                    currentInit = "tlInputSI0" + (this.maxChecks + 1) + "_R" + y;
                    initiativesAvail = (this.calcService.getValue(currentInit) != undefined);
                }
            }
        }

        //Updated enable/disable as well as maxCheck and totalchecks
        for (let i = 0; i < this.initiativesArray.length; i++) {
            if (this.initiativesArray[i].isChecked == this.currentYear) {
                this.totalChecked++;
                this.initiativesArray[i].isEnabled = true;
            }
        }

        for (let i = 0; i < this.initiativesArray.length; i++) {
            if (this.initiativesArray[i].isChecked != this.currentYear) {
                if (this.totalChecked == this.maxChecks) {
                    this.initiativesArray[i].isEnabled = false;
                }
            }
        }
    }

    // clicked on "checkbox" to make/unmake initiative choice for the year
    // the .isChecked attribute contains the year in which the initiative was chosen (1, 2, ...)
    onCheck($event, index, ID) {
        $event.stopPropagation();

        // check/uncheck initiative
        if (this.initiativesArray[index].isEnabled) {
            this.initiativesArray[index].isChecked = (this.initiativesArray[index].isChecked > 0 ? 0 : this.currentYear);

            // reset initiative model ranges before setting new ones
            for (let i = 1; i <= this.maxChecks; i++) {
                let currentInit = "tlInputSI0" + i + "_R" + this.currentYear;
                this.calcService.setValue(currentInit, 0);
            }

            this.totalChecked = 0;
            for (let i = 0; i < this.initiativesArray.length; i++) {
                // if initiative was checked this year
                if (this.initiativesArray[i].isChecked == this.currentYear) {
                    this.totalChecked++;
                    // set it in the next slot in the model for this year
                    let currentInit = "tlInputSI0" + this.totalChecked + "_R" + this.currentYear;
                    let initValue = this.initiativesArray[i].ID.replace("INIT", "SI")//"SI"+(i + 1)
                    this.calcService.setValue(currentInit, initValue, false, null, 'initiative');
                }
            }

            for (let i = 0; i < this.initiativesArray.length; i++) {
                this.initiativesArray[i].isEnabled = (
                    (this.initiativesArray[i].isChecked == this.currentYear) ||
                    ((this.initiativesArray[i].isChecked == 0) && (this.totalChecked < this.maxChecks)));
            }
        }
    }

    onModelChange() {
        //Will update automatically only for follower
        // if (!this.userIsForeman) {
            this.initiativesArray.map(x => x.isChecked = 0);
            for (let y = 1; y <= this.currentYear; y++) {
                this.maxChecks = 0;
                this.totalChecked = 0;
                let currentInit = "tlInputSI0" + (this.maxChecks + 1) + "_R" + y;
                let initiativesAvail = (this.calcService.getValue(currentInit) != undefined);
                while (initiativesAvail && (this.maxChecks < 10)) {
                    this.maxChecks++;
                    let currentIndex = Number(this.calcService.getValue(currentInit).replace('SI', '')) - 1;
                    if (currentIndex >= 0) {
                        this.initiativesArray[currentIndex].isChecked = y;
                        this.totalChecked++
                        if (y < this.currentYear) {
                            this.initiativesArray[currentIndex].isEnabled = false;
                        }
                    } else if (this.initiativesArray[currentIndex]) {
                        this.initiativesArray[currentIndex].isChecked = 0;
                    }
                    currentInit = "tlInputSI0" + (this.maxChecks + 1) + "_R" + y;
                    initiativesAvail = (this.calcService.getValue(currentInit) != undefined);
                }
            }
        // }
    }

    getCategoryClass(category: string){
        return this.categories.indexOf(category) + 1;
    }

    getFilters() {
        return this.filters.map(i => 'cat-' + i).join(" ");
    }

    toggleFilter(ci:string) {
        this.hideCardAnimation = true;
        let filterIndex = this.filters.indexOf(ci);
        if (filterIndex === -1) {
            this.filters.push(ci);
        }
        else {
            this.filters.splice(filterIndex, 1);
        }
        this.isFiltered = (this.filters.length !== 0) ? true : false;
        setTimeout(() => {
            this.hideCardAnimation = false
        }, 1000);
    }

    handleKeyboardEvent(event: KeyboardEvent, actionType: string, ...args) {
        if (event.keyCode === 13 || event.keyCode === 32) {
            event.preventDefault();
            event.stopPropagation();
            switch (actionType) {
                case 'togglefilter':
                    this.toggleFilter(args[0]);
                    break;
                case 'checkInitiative':
                    this.onCheck(event,args[0],args[1])
                    break;
                case 'selectInitiative':
                    args[0].isSelected = true;
                    break;
                case 'deSelectInitiative':
                    args[0].isSelected = false;
                    break;
            }
        }
    }

    filterByYear(_year, _initiative) {
        return _initiative.filter(_in => {
            return _in.isChecked == _year;
        })
    }
}
