import { Component, OnInit, HostListener, Input } from '@angular/core';

@Component({
  selector: 'app-tutorial-data',
  templateUrl: './tutorial-data.component.html',
  styleUrls: ['./tutorial-data.component.styl']
})
export class TutorialDataComponent implements OnInit {
  public activeIndex: number = 0;
  navIndex: any = '0';
  @Input() tutorialData;
  @Input() isInsidePopup = false;
  tutorialObj;

  @HostListener('document:keydown.arrowup', ['$event'])
  @HostListener('document:keydown.arrowdown', ['$event'])
  @HostListener('document:keydown.arrowleft', ['$event'])
  @HostListener('document:keydown.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        $('.tutorials-link:focus').next().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
      case 37:
      case 38:
        $('.tutorials-link:focus').prev().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
    }
  }

  constructor() { }

  ngOnInit() {
    this.tutorialObj = this.processTutorialData(this.tutorialData);
  }

  processTutorialData(rawData) {
    var localChoices = [];

    let localObj = rawData;
    let keyArr: any[] = Object.keys(localObj);
    // let keyArr: any[] = localObj.keys();

    let i = 1;
    let moreChoices = false;
    keyArr.forEach((key: any) => {
      if (key.indexOf("tab" + i) != -1) {
        moreChoices = true;
      }
    });

    while (moreChoices) {
      let localName = ""
      let localContent = "";
      let localImage = "";
      let localAdditionalImage = "";
      let localAdditionalData = {};
      keyArr.forEach((key: any) => {
        if ((key.indexOf("tab" + i) != -1) && (key.indexOf("_name") > -1)) {
          localName = localObj[key];
        } else if ((key.indexOf("tab" + i) != -1) && (key.indexOf("_content") > -1)) {
          localContent = localObj[key];
        } else if ((key.indexOf("tab" + i) != -1) && (key.indexOf("_image") > -1)) {
          localImage = localObj[key];
        }else if ((key.indexOf("tab" + i) != -1) && (key.indexOf("_additionalImage") > -1)){
          localAdditionalImage = localObj[key];
        } else if((key.indexOf("tab" + i) != -1)){
          localAdditionalData[key] = localObj[key]
        }
      });

      localChoices.push({
        "name": localName,
        "content": localContent,
        "image": localImage,
        "additionalImage":localAdditionalImage,
        "addtional": localAdditionalData
      });
      i++;
      moreChoices = false;
      keyArr.forEach((key: any) => {
        if (key.indexOf("tab" + i) != -1) {
          moreChoices = true;
        }
      });
    }
    return localChoices;
  }

}
