import { Component, OnInit } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'app-competitor',
  templateUrl: './competitor.component.html',
  styleUrls: ['./competitor.component.styl']
})
export class CompetitorComponent implements OnInit {
  currentRound;

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.currentRound = this.calcService.getValue('tlInputCurrentRound')
  }

}
