import { ChangeDetectionStrategy, Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { LocationService } from 'src/app/shared/location.service';
import { CalcService } from '@btsdigital/ngx-isomer-core';


@Component({
    selector: 'reports',
    templateUrl: './reports.html',
    styleUrls: ['./reports.styl']
})

export class Reports implements OnInit, OnDestroy {

    public activeTab: string;
    private subscription: Subscription;
    navIndex: any = '0';
    assertiveText: string = '';
    public currentYear: number;

    @HostListener('document:keydown.arrowup', ['$event'])
    @HostListener('document:keydown.arrowdown', ['$event'])
    @HostListener('document:keydown.arrowleft', ['$event'])
    @HostListener('document:keydown.arrowright', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 39:
            case 40:
                $('.nav-item:focus').next('.nav-item:not(.disabled)').focus();
                
                event.preventDefault();
                event.stopPropagation();
                break;
            case 37:
            case 38:
                $('.nav-item:focus').prev('.nav-item:not(.disabled)').focus();
                event.preventDefault();
                event.stopPropagation();
                break;
        }
    }

    constructor(
        public activatedRoute: ActivatedRoute,
        private router: Router,
        private locationService: LocationService,
        private calcService: CalcService
        ) {

        this.subscription = activatedRoute.params.subscribe((param: any) => {
            let activeTab = param['tabid'] ? param['tabid'] : null;
            this.activeTab = activeTab;
        });
    }

    ngOnInit() {
        this.currentYear = Number(this.calcService.getValue('tlInputCurrentRound'));
    }

    ngOnDestroy() {
        if(this.subscription){
            this.subscription.unsubscribe();
        }
    }

    setactiveTab(e, _subTab: string): any {
        if (_subTab === null) return;
        this.assertiveText = _subTab + (e.target.classList.contains('active') ? 'selected' : 'not selected');
        const url = '/dashboard/reports/' + _subTab;
        this.locationService.setLocation(url, 0);
        this.router.navigate([url]);
    }

}
