import { Component, OnInit, ViewChild, OnDestroy, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Subscription, fromEvent } from 'rxjs';
import { SyncStatusService, CalcService } from '@btsdigital/ngx-isomer-core';
import { CommonModalComponent } from '../common-modal/common-modal.component';

@Component({
  selector: 'ism-network-error',
  templateUrl: './network-error.component.html',
  styleUrls: ['./network-error.component.styl']
})
export class NetworkErrorComponent implements OnInit, OnDestroy {
  @ViewChild('networkErrorModal', { static: true }) public networkErrorModalRef: CommonModalComponent;
  networkSubscriber: Subscription;
  private status: number;


  constructor(private syncStatusService: SyncStatusService, private calcService: CalcService) { }

  ngOnInit() {
    this.attachNetworkPopup();
  }

  attachNetworkPopup() {
    this.networkSubscriber = this.syncStatusService.Status.subscribe((status: any) => {
      this.status = status;
      this.checkNetWorkPopup();
    });

    let online$ = fromEvent(window, 'online');
    let offline$ = fromEvent(window, 'offline');

    online$.subscribe(e => {
      this.calcService.setValue('tlInputCurrentRound', this.calcService.getValue('tlInputCurrentRound'));
      // this.calcService.setValue(AppConstants.REFS.CURRENT_ROUND,this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND));
    });

    offline$.subscribe(e => {
      this.showNetworkAlert();
    });
  }

  checkNetWorkPopup() {
    if (this.status == 2 || this.status == 3) {
      this.showNetworkAlert();
    }

  }

  showNetworkAlert() {
    this.networkErrorModalRef.openModal();
  }

  hideNetworkAlert() {
    this.networkErrorModalRef.closeModal();
  }

  ngOnDestroy() {
    if (this.networkSubscriber) {
      this.networkSubscriber.unsubscribe();
    }
  }

}
