import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { FileSaver } from './import-export-services/filesaver';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { AppConfig } from 'src/app/app.config';
import { CommonModalComponent } from '../shared/common-modal/common-modal.component';
import { AppConstants } from '../app-constants';
import { Subscription } from 'rxjs';

const KEY_NUM_5 = 53; // used to export TESTING TEMPLATE Inputs and Outputs
const KEY_NUM_6 = 54; // used to export Inputs
const KEY_NUM_7 = 55; // used to import data
const KEY_NUM_8 = 56; // used to export Outputs

@Component({
  selector: 'ism-import-export',
  templateUrl: './import-export.component.html',
  styleUrls: ['./import-export.component.styl']
})
export class ImportExportComponent implements OnInit {
  triggerListener: Subscription;
  @ViewChild('importbalances', { static: true }) importBalancesEl;
  @ViewChild('importsuccess', { static: true }) importsuccessModalRef: CommonModalComponent;
 
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode == KEY_NUM_5) {
      this.createTestTemplate();
    }
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode == KEY_NUM_6) {
      this.exportDecisions();
    }
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode == KEY_NUM_7) {
      this.importBalances();
    }
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode == KEY_NUM_8) {
      this.exportOutputs();
    }
  }


  constructor(
    private fileSaver: FileSaver,
    private calcService: CalcService,
    private appConfig: AppConfig,
    private communicator: CommunicatorService
  ) { }

  ngOnInit() {
    this.triggerListener = this.communicator.getEmitter(AppConstants.EVENTS.IMPORT_EXPORT_TRIGGER).subscribe((value)=>{
      if (value == 'Testing') {
        this.createTestTemplate();
      }
      if (value == 'Inputs') {
        this.exportDecisions();
      }
      if (value == 'Import') {
        this.importBalances();
      }
      if (value == 'Outputs') {
        this.exportOutputs();
      }
    })
  }
  
  ngOnDestroy(){
    if(this.triggerListener){
      this.triggerListener.unsubscribe()
    }
  }

  createTestTemplate(){
    const teamName = this.calcService.getValue('tlInputTeamNumber_R1');
    let initialStaticData = {
      "Candidate Name": "TEST",
      'Email': "USER 1"
    }
    let modelState;
    let fileName;
    const delim = ',';
    const fileNamePrefix = this.appConfig.configuration.eventTitle;
    //Inputs
    modelState = this.calcService.exportData('^tlInput.+$');
    let allInputs = {...initialStaticData, ...modelState}

    fileName = fileNamePrefix + '_INPUTS_TEAM_' + teamName + '.csv';
    this.fileSaver.fileSaveAs(fileName, allInputs, delim, undefined, true);

    //Outputs
    modelState = this.calcService.exportData('^tlOutput.+$');
    let allOutputs = {...initialStaticData, ...modelState}

    fileName = fileNamePrefix + '_OUTPUTS_TEAM_' + teamName + '.csv';
    this.fileSaver.fileSaveAs(fileName, allOutputs, delim, undefined, true);
  }

  exportOutputs(){
    const modelState = this.calcService.exportData('^tlOutput.+$');
    const teamName = this.calcService.getValue('tlInputTeamNumber_R1');
    this.exportDecisionsOfISM(modelState, teamName);
  }

  // local export data method to work with ism-calc-service
  exportDecisions() {
    // this.calcService.exportData(true, this.exportRangeNames);
    const modelState = this.calcService.exportData('^tlInput.+$');
    const teamName = this.calcService.getValue('tlInputTeamNumber_R1');
    this.exportDecisionsOfISM(modelState, teamName);
    // this.exportDecisionsOfISM(modelState, '');
  }

  // For values from ISM
  exportDecisionsOfISM(modelState, teamName, transpose = false) {
    const self = this;
    // const fileNamePrefix = 'YayCo_';
    const fileNamePrefix = this.appConfig.configuration.eventTitle;
    const fileName = fileNamePrefix + '_Dec_TEAM_' + teamName + '.csv';
    const delim = ',';
    self.fileSaver.fileSaveAs(fileName, modelState, delim);
  }

  importBalances() {
    this.importBalancesEl.showFileChooser();
  }

  onFileLoaded(jsonOb) {
    const self = this;
    console.log('Loaded file', jsonOb);

    return new Promise((resolve, reject) => {
        try {
          this.calcService.appendDataToModel(jsonOb).then(() => {
          self.showSuccessAlert();
          resolve();
          }, reject);
        } catch (err) {
          reject(err);
        }
    });
  }

  showSuccessAlert() {
    this.importsuccessModalRef.openModal();
  }

  hideSuccessAlert() {
    this.importsuccessModalRef.closeModal();
  }

}
