import { Injectable } from '@angular/core';
import {
  CalcService, CommunicatorService, Constants, HttpWrapperService,
  IsomerXapiService, ManifestService, StorageService,
  SyncService, SyncStatusService, TextService
} from '@btsdigital/ngx-isomer-core';
import { SignalRService } from '@btsdigital/pulsesignalr';
import { ChannelEventName, LoggerService, XApiService } from '@btsdigital/pulseutilities';
import { environment } from '../../environments/environment';
import { ModelLoaderService } from '../model/model-loader.service';
import { NotifyService } from './notification/notify.component';
import { APP_READY } from './utils/utils';
import { AppConfig } from '../app.config';

@Injectable()
export class BootstrapService {

  private textServiceInitPromise: Promise<any>;
  private modelLoadCompletePromise: Promise<any>;
  private isAppReady = false;
  private appConfigInitialized = false;
  userObj: any;

  constructor(private calcService: CalcService, private textService: TextService,
    private storageService: StorageService, private syncStatusService: SyncStatusService,
    private communicator: CommunicatorService, private logger: LoggerService, private appConfig: AppConfig,
    private modelLoader: ModelLoaderService, private signalR: SignalRService, private syncService: SyncService,
    private manifestService: ManifestService, private httpWrapperService: HttpWrapperService,
    private xpiService: XApiService, private isomerXapiService: IsomerXapiService, private notifyService: NotifyService) {

    this.userObj = JSON.parse(localStorage.getItem('user'));
    this.xpiService.configureAngularticsUrl(this.appConfig.configuration.simConfig.connectToKafka ? this.appConfig.configuration.kafkaUrl : this.appConfig.configuration.nodejsUrl,
      this.appConfig.configuration.simConfig.connectToKafka, this.appConfig.configuration.simConfig.simId, this.appConfig.configuration.simConfig.apiBuildVersion);
  }

  initAppConfig() {
    let simConfig = this.appConfig.configuration.simConfig
    this.manifestService.setConfig({ config: this.appConfig.configuration, simConfig });
    this.httpWrapperService.setHostName(this.appConfig.configuration.hostName);
    this.signalR.configureSignalRService({ hostname: this.appConfig.configuration.hostName, serviceUrl: this.appConfig.configuration.serviceUrl, hasHeartBeat: true });
    this.isomerXapiService.configureIsomerAppService({
      enabletracking: this.appConfig.configuration.enableTracking, statusCompletionQuestions: this.appConfig.configuration.statusCompletionQuestions,
      rangeRefTags: this.appConfig.configuration.rangeRefTags, enableLoadLevelTracking: this.appConfig.configuration.enableLoadLevelTracking
    });

    // set the user for tracking
    if (this.appConfig.configuration.enableTracking) {
      try {
        if (this.userObj) {
          let serviceUrl = this.appConfig.configuration.serviceUrl;
          if (this.appConfig.configuration.simConfig.connectToKafka) {
            serviceUrl = this.appConfig.configuration.hostName + this.appConfig.configuration.simConfig.apiPrefix;
          }

          this.xpiService.setUserProperties(this.userObj.pid, this.userObj.pname, this.userObj.pemail, this.userObj.planguagekey,
            this.userObj.token, this.userObj.moduleName, this.userObj.userExperience, this.appConfig.configuration.hostName, this.appConfig.configuration.activityType,
            this.userObj.userExperience, this.appConfig.configuration.platform, this.appConfig.configuration.defaultEmail, this.appConfig.configuration.lrsUrl, serviceUrl,
            this.appConfig.configuration.enableEmailEncryption);
        }
      } catch (e) {
        this.logger.log('Error initializing UserProperties: ' + e);
      }
    }
    // }
    this.logger.enableLogging(this.appConfig.configuration.logging);
    // set mode for storageService
    this.storageService.setMode(this.appConfig.configuration.connectToPulse ? Constants.STORAGE_MODES.MIXED : Constants.STORAGE_MODES.LOCAL);
    this.appConfigInitialized = true;
    this.alreadyLoggedInAlert();
  }

  alreadyLoggedInAlert() {
    setTimeout(() => {
      // Multi login
      if (this.signalR.subscribeToEvent(ChannelEventName.LogoutPreviousUser)) {
        this.signalR.subscribeToEvent(ChannelEventName.LogoutPreviousUser)
          .subscribe(item => {
            if (item && item.EventName === 'LogoutPreviousUser') {
              if (item.Data && item.Data.participantEmail === this.userObj.pemail) {
                if (!localStorage.getItem('alreadyUserLoggedIn')) {
                  this.communicator.trigger('alertUserLoggedIn', true);
                }
              }
            }
          });
      }
      // Receive message from DDv2
      // if (this.signalR.subscribeToEvent(ChannelEventName.SendMessage)) {
      //   this.signalR.subscribeToEvent(ChannelEventName.SendMessage)
      //     .subscribe((item: any) => {
      //       if (item && item.EventName === 'SendMessage') {
      //         if (item && item.TargetParticipationId === this.userObj.pid) {
      //           if (item && item.Data && item.Data.Data && item.Data.Data.message) {
      //             this.communicator.trigger('sendMessage', item.Data.Data.message);
      //           }
      //         }
      //       }
      //     });
      // }
    }, 5000);
  }

  init(): Promise<any> {
    if (!this.appConfigInitialized) {
      this.initAppConfig();
    }
    return this.modelLoader.getModel().then((response) => {
      const model = response;
      // set initial language to be loaded
      this.textService.language = (this.userObj && this.userObj.planguagekey && this.userObj.planguagekey !== '') ? this.userObj.planguagekey : 'en';
      // load text content
      this.textServiceInitPromise = this.textService.init();

      this.notifyService.notificationData['flag'] = true;
      this.notifyService.notificationData['message'] = 'Model Loaded Successfully';
      this.notifyService.notificationData['display'] = true;

      // try and load the calc model
      this.modelLoadCompletePromise = this.calcService
        .getApi(model)
        .catch((err) => {
          this.logger.log('Error when loading model ', err);
          this.notifyService.notificationData['flag'] = false;
          this.notifyService.notificationData['message'] = err;
          this.notifyService.notificationData['display'] = true;
        });

      return Promise.all([this.textServiceInitPromise, this.modelLoadCompletePromise])
        .then(() => {
          if (this.appConfig.configuration.connectToPulse) {
            this.syncStatusService.SetModelStateStatus(0);
            return this.syncService.initializeSync().then(() => {
              return this.syncService.setMode(Constants.CONNECTION_MODE.PUSH).then(() => {
                this.appReady = true;
              });
            });
          } else {
            this.appReady = true
          }
        });
    });
  }

  // private onProgress(progress: string) {
  //   this.logger.log('Progress ' + progress);
  // }

  public set appReady(v: boolean) {
    this.isAppReady = v;
    this.communicator.trigger(APP_READY, v);
  }

  public get appReady(): boolean {
    return this.isAppReady;
  }
}
