import { Injectable } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Utils } from '../../../shared/utils/utils';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

@Injectable()
export class ChoiceMadeGuard implements CanActivate {
    constructor(private router: Router, private calcService: CalcService, private utils: Utils) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        let id: string = route.params['id'];
        
        if (this.calcService.getValue('tlinputRead' + id) === "1") {
            this.router.navigate(this.utils.generateFeedbackUrl(id));
            return false;
        }
        return true;
    }
}
