import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../environments/environment';

@Injectable()
export class AppConfig {
    public configuration: any;
    public set setConfig(config) {
        this.configuration = config;
        this.updateHostURL()
    }

    constructor(private http: HttpClient) { }

    public load() {
        return this.http.get('./config.json');
    }

    updateHostURL() {
        if (this.configuration.CDN0LOCAL1LOCALHOST2 != undefined) {
            const hostName: string = (this.configuration.CDN0LOCAL1LOCALHOST2 === 0 ?
                'https://' + this.configuration.hostname + '/' : (this.configuration.CDN0LOCAL1LOCALHOST2 === 1 ?
                    window.location.protocol + '//' + window.location.hostname : 'http://localhost'));

            this.configuration.hostName = hostName;
            this.configuration.serviceUrl = hostName + "/PulseServices/";
        }
    }
}
