import { Component, OnInit, OnDestroy, HostListener } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { TextService, StorageService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { RESOURCES_ROUTE, Utils } from '../../shared/utils/utils';
import { Subscription } from 'rxjs';
import { LocationService } from 'src/app/shared/location.service';
import * as $ from 'jquery';

@Component({
    selector: 'resources',
    templateUrl: './resources.html',
    styleUrls: ['./resources.styl'],
    providers: []
})

export class Resources implements OnInit, OnDestroy {
    public activeTab: string;
    public planningStepsText: string = "";
    public guideTabText: Array<any> = [];
    private subscription: Subscription;
    navIndex: any = '0';
    assertiveText: string = '';

    @HostListener('document:keydown.arrowup', ['$event'])
    @HostListener('document:keydown.arrowdown', ['$event'])
    @HostListener('document:keydown.arrowleft', ['$event'])
    @HostListener('document:keydown.arrowright', ['$event'])
    public handleKeyboardEvent(event) {
        switch (event.keyCode) {
            case 39:
            case 40:
                $('.nav-item:focus').next().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
            case 37:
            case 38:
                $('.nav-item:focus').prev().focus();
                event.preventDefault();
                event.stopPropagation();
                break;
        }
    }

    constructor(
        private textengineService: TextService, 
        private router: Router, 
        private utils: Utils,
        activatedRoute: ActivatedRoute,
        private storageService: StorageService,
        private locationService: LocationService,
        private communicator: CommunicatorService
    ) {
        this.subscription = activatedRoute.params.subscribe((param: any) => {
            let activeTab = (param['tabid']) ? param['tabid'] : null;
            this.activeTab = activeTab;
        });
    }

    ngOnInit() {

        this.planningStepsText = this.textengineService.getText("PlanningStepsPage1Text");
        this.guideTabText.push(this.textengineService.getText("GuidePage1Text"));
        this.guideTabText.push(this.textengineService.getText("GuidePage3Text"));
        this.guideTabText.push(this.textengineService.getText("GuidePage4Text"));
        this.guideTabText.push(this.textengineService.getText("GuidePage5Text"));

        this.locationService.setLocation(this.router.url, this.activeTab);
    }

    setactiveTab(e, _subTab: string): any {
        if (_subTab === null) return;
        this.assertiveText = _subTab + (e.target.classList.contains('active') ? 'selected' : 'not selected');
        const url = 'dashboard/resources/' + _subTab;
        this.locationService.setLocation(url, 0)
        this.router.navigate([url]);
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }
}
