import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { CalcService, NumberFormattingPipe, TextService } from '@btsdigital/ngx-isomer-core';
import * as _ from 'lodash';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Subscription } from 'rxjs';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';
import * as Collections from 'typescript-collections';
import { LocationService } from '../../../shared/location.service';
import { MESSAGES_OUTLET, Utils } from '../../../shared/utils/utils';

@Component({
    selector: 'info',
    templateUrl: './info.html',
    styleUrls: ['./info.styl'],
    host: {
        '[style.display]': "'block'"
    }
})
export class InfoComponent implements OnInit, OnDestroy {
    el: HTMLElement;
    title = "Info component !!!";
    id: string;
    message: Collections.Dictionary<string, string>;
    isClosing = false;
    routerObserver: Subscription;
    imageHeader = '';

    tutorialData;
    tutorialName: string;

    chartType = "";
    @Input() rangeref = "";
    @Input() categorylabels = "";
    @Input() serieslabels = "";
    @Input() year = "";
    @Input() numberformat = "";
    @Input() charttitle = "";
    @Input() chartoptions: any = {};
    @Input() min = "";
    @Input() max = "";

    localRangeRefArray: any;
    localCategoryLabelsArray: any;
    localSeriesLabelsArray: any;

    chartRangeRef: any;
    chartXAxis: any;
    chartSeriesOptions: any;
    chartYAxis: any;
    @ViewChild('infoModal', {static: true}) childModal: CommonModalComponent;

    showMarketReport = false;

    constructor(private textengineService: TextService, private calcService: CalcService, private elRef: ElementRef, private route: ActivatedRoute, private router: Router, private utils: Utils, private locationService: LocationService, private numberFormatting: NumberFormattingPipe) { };

    ngOnInit() {
        this.el = this.elRef.nativeElement;
        this.isClosing = false;
        this.route.params.forEach(params => this.id = params['id']);
        this.title += " " + this.id;
        this.message = this.textengineService.getScene(this.id);

        this.imageHeader = (((this.message.getValue("image") != undefined) && (this.message.getValue("image") != "")) ? "assets/images/" + this.message.getValue("image") : "");

        // check if there is tutorial present
        let tutorialID = this.message.getValue("tutorial");
        if (tutorialID) {
            let tutorialScene = this.textengineService.getScene(tutorialID)
            this.tutorialData = this.utils.singleDictToObj(tutorialScene);
        }

        // check if there is market update
        this.showMarketReport = this.message.getValue("showMarketReport") == "true";

        this.rangeref = this.message.getValue("chartranges");
        this.categorylabels = this.message.getValue("chartcategories");
        this.serieslabels = this.message.getValue("chartseries");
        this.year = this.message.getValue("chartyear");
        this.numberformat = this.message.getValue("chartnumberformat");
        this.charttitle = this.message.getValue("charttitle");
        if (this.message.getValue("chartoptions") && (this.message.getValue("chartoptions") !== "")) {
            try {
                this.chartoptions = JSON.parse(this.message.getValue("chartoptions").replace(/'/g, '"'));
            }
            catch (e) {
                this.chartoptions = {};
            }
        }
        this.min = this.message.getValue("min");
        this.max = this.message.getValue("max");
        this.chartType = this.message.getValue("chart");
        this.processComponentInputs();
        _.merge(this.chartoptions, {
            legend: { align: 'right', layout: 'vertical', verticalAlign: 'middle' }
        });

        // this.chartoptions = JSON.stringify(this.chartoptions);

        this.routerObserver = this.router.events.subscribe((event) => {
            if (event instanceof NavigationEnd) {
                this.route.params.forEach(params => this.id = params['id']);
                this.title += " " + this.id;
                this.message = this.textengineService.getScene(this.id);
            }
        });
    }

    onClose() {
        this.calcService.setValue("tlinputDec" + this.message.getValue("ID"), 1);
        this.calcService.setValue("tlinputRead" + this.message.getValue("ID"), 1);
        // this.toggleClass(true);
        // setTimeout(() => this.router.dispose(), 1000);
        this.isClosing = true;
        setTimeout(() => {
            // this.router.navigateByUrl(this.utils.stripOutletFromUrl(this.router.url, MESSAGES_OUTLET))
            let url = this.utils.stripOutletFromUrl(this.router.url, '');
            this.locationService.setLocation(url, 1);
        }, 1000);
        this.router.navigateByUrl('/dashboard');
        this.childModal.closeModal();
    }

    ngOnDestroy() {
        this.isClosing = false;
        this.routerObserver.unsubscribe();
    }

    getChartRangeRef() {
        let rangeref = [];
        switch (this.chartType) {
            case 'basicbar':
            case 'basicline':
            case 'basiccolumn':
            case 'stackedcolumn':
                rangeref = _.chunk(this.localRangeRefArray, this.localCategoryLabelsArray.length);
                this.chartRangeRef = rangeref;
                break;
            default:
                this.chartRangeRef = this.localRangeRefArray
                break;
        }
    }

    getChartSeries() {
        const series = [];
        switch (this.chartType) {
            case 'basicbar':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'bar', name: this.textengineService.getText(n) });
                });
                this.chartSeriesOptions = series;
                break;
            case 'basicline':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'line', name: this.textengineService.getText(n) });
                });
                this.chartSeriesOptions = series;
                break;
            case 'basiccolumn':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'column', name: this.textengineService.getText(n) });
                });
                this.chartSeriesOptions = series;
                break;
            case 'stackedcolumn':
                _.each(this.localSeriesLabelsArray, (n) => {
                    series.push({ type: 'stackedcolumn', name: this.textengineService.getText(n) });
                });
                this.chartSeriesOptions = series;
                break;
            default:
                this.chartSeriesOptions = this.localSeriesLabelsArray;
                break;
        }
    }

    getChartCategories() {
        const categories = { categories: null };
        categories.categories = this.localCategoryLabelsArray;
        this.chartXAxis = categories;
    }

    getYAxisData() {
        this.chartYAxis = {
            labels: {
                format: '{value:' + this.numberformat + '}'
            },
            title: { enabled: false },
            alternateGridColor: 'rgb(240, 241, 242)'
        };

        switch (this.chartType) {
            case 'stackedcolumn':
                this.chartYAxis['stackLabels'] = {
                    enabled: false
                };
                break;
        }
    }

    getChartOptions() {
        const self = this
        let chartOptions: any = {};
        switch (this.chartType) {
            case 'basicbar':
                chartOptions = {
                    chart: { width: '800' },
                    plotOptions: {
                        bar: {
                            dataLabels: {
                                enabled: false,
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    }
                };
                break;
            case 'basicline':
                chartOptions = {
                    chart: { width: '800' },
                    plotOptions: {
                        line: {
                            dataLabels: {
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    }
                };
                break;
            case 'basiccolumn':
                chartOptions = {
                    chart: { width: '800' },
                    plotOptions: {
                        column: {
                            dataLabels: {
                                enabled: false
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    }
                };
                break;
            case 'stackedcolumn':
                chartOptions = {
                    chart: { width: '800' },
                    plotOptions: {
                        column: {
                            dataLabels: {
                                format: '{y:' + this.numberformat + '}'
                            },
                            tooltip: {
                                pointFormat: '{point.y:' + this.numberformat + '}'
                            }
                        }
                    },
                    tooltip: {
                        headerFormat: '<b>{point.x}</b><br/>',
                        pointFormatter: function () {
                            if (self.numberformat) {
                                return this.series.name + ": " + self.numberFormatting.transform(this.y, self.numberformat) + "<br />" + "Total: " + self.numberFormatting.transform(this.total, self.numberformat);
                            } else {
                                return this.series.name + ": " + this.y + "<br/>Total: " + this.stackTotal;
                            }
                        }
                    }
                };
                break;
        }
        _.merge(this.chartoptions, chartOptions);
    }

    processComponentInputs() {
        let self = this,
            localRangeRef = self.rangeref,
            localCategoryLabels = self.categorylabels,
            localSeriesLabels = self.serieslabels;

        if (localRangeRef != undefined && localRangeRef != null) {
            localRangeRef = self.rangeref.toString();
            localRangeRef = localRangeRef.replace(/\[|\]/g, '');
            localRangeRef = localRangeRef.replace(/ /g, '');
            this.localRangeRefArray = localRangeRef.split(",");
        }

        if (localCategoryLabels != undefined && localCategoryLabels != null) {
            localCategoryLabels = self.categorylabels.toString();
            localCategoryLabels = localCategoryLabels.replace(/\[|\]/g, '');
            localCategoryLabels = localCategoryLabels.replace(/ /g, '');
            this.localCategoryLabelsArray = localCategoryLabels.split(",");
        }

        if (localSeriesLabels != undefined && localSeriesLabels != null) {
            localSeriesLabels = self.serieslabels.toString();
            localSeriesLabels = localSeriesLabels.replace(/\[|\]/g, '');
            localSeriesLabels = localSeriesLabels.replace(/ /g, '');
            this.localSeriesLabelsArray = localSeriesLabels.split(",");
        }

        this.getChartRangeRef();
        this.getChartSeries();
        this.getChartCategories();
        this.getYAxisData();
        this.getChartOptions();
    }

    ngAfterViewInit() {
        this.showModal();
    }

    showModal() {
        this.childModal.openModal();
    }
}
