import { Component, OnInit, Input, ViewChildren, QueryList, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { CalcService, TextService, SyncService } from '@btsdigital/ngx-isomer-core';
import { environment } from 'src/environments/environment';
import * as _ from 'lodash';
import { SwotInput } from '../swot-input/swot-input';

@Component({
  selector: 'swot-container',
  templateUrl: './swot-container.component.html',
  styleUrls: ['./swot-container.component.styl']
})
export class SwotContainerComponent implements OnInit, AfterViewInit {
  @Input() maxInputs = 5
  @Input() modelRangePrefix: string;
  @Input() swottitle: string = "";
  @Input() initialTextKeyPrefix: string;
  @Input() isReadOnly = false;
  @Input() tabIndex?: string = '0';

  emptyArr;
  hideAddButton: boolean = false;

  @ViewChild('scrollContainer',{static: true}) scrollContainerRef: ElementRef
  @ViewChildren('swotInputInstance') swotInputInstances: QueryList<SwotInput>
  
  constructor(private calcService: CalcService, private textEngine: TextService, private syncService: SyncService) { }

  ngOnInit() {
    // this.calcService.setValue(this.modelRange, 1);
    this.initialize();
  }

  initialize(){
    this.emptyArr = _.range(this.maxInputs);
  }

  ngAfterViewInit(){
    this.checkAddInputStatus()
  }

  ngOnDestroy() {
  }

  addInput() {
    let arr = this.swotInputInstances.toArray();
    for(let i=0; i<arr.length; i++){
      if(!arr[i].isVisible){
        arr[i].setVisibile = true
        arr[i].onEditClick();
        //scroll to
        // this.scrollContainerRef.nativeElement.scrollTop = arr[i].elRef.nativeElement.scrollTop
        break
      }
    }

    this.checkAddInputStatus();
  }

  checkAddInputStatus(){
    let arr = this.swotInputInstances.toArray();
    let count = 0
    for(let i=0; i<arr.length; i++){
      if(arr[i].isVisible){
        count++
      }
    }
    
    this.hideAddButton = count == arr.length;
  }

  deleteBtnClicked(_inputIdx) {
    let arr = this.swotInputInstances.toArray();
    arr = arr.filter((ar) => {
      return ar.isVisible;
    });
    for (let i = _inputIdx; i < arr.length; i++) {
      if (i == (arr.length - 1) && arr[i].isVisible) {
        this.calcService.setValue(arr[i].ref, null);
        arr[i].setVisibile = false;
      } else {
        this.calcService.setValue(arr[i].ref, arr[i + 1].inputText);
      }
    }
    this.checkAddInputStatus();
    

  }

}
