import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
// import { CustomTextService as TextService } from '../boot/text-service.extended';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { CalcService, TextService,SyncService } from '@btsdigital/ngx-isomer-core';
import * as Collections from 'typescript-collections';
import { LocationService } from '../../../shared/location.service';
import { StorageService } from '../../../shared/storage.service';
import { MEETINGS_OUTLET, Utils } from '../../../shared/utils/utils';
import { Chooser } from './chooser';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';

@Component({
    selector: 'user-choice',
    templateUrl: './user-choice.html',
    styleUrls: ['./user-choice.styl'],
    // animations: [
    //     trigger('routeAnimation', [
    //         state('*', style({ transform: 'translateX(100%)', opacity: 1 })),
    //         transition('void => *', [
    //             style({ transform: 'translateX(200%)', opacity: 1 }),
    //             animate(1000)
    //         ]),
    //         transition('* => void', [
    //             animate(1000, style({ transform: 'translateX(200%)', opacity: 0 }))
    //         ])
    //     ])
    // ],
    host: {
        // '[@routeAnimation]': 'true',
        '[style.display]': "'block'"
    }
})
export class ChoiceComponent implements OnInit, OnDestroy, AfterViewInit {
    public eventType: string = "";
    public isClosing: boolean = false;
    public id: string;
    public scene: Collections.Dictionary<string, string>;
    private choices = [];
    private isViewed = false;
    private answer: number = -1;
    private model: string;
    private title: string;
    @ViewChild('chooser', { static: false }) chooser: Chooser;
    @ViewChild('lgModal', {static: true}) childModal: CommonModalComponent;
    private subscription;
    private disableDashEvent;
    public userIsForeman = true;
    constructor(private textEngineService: TextService, private calcService: CalcService, private route: ActivatedRoute, private router: Router, private utils: Utils, private storageService: StorageService, private locationService: LocationService,private syncService: SyncService,
        private appConfig: AppConfig) { }

    ngOnInit() {
        this.isClosing = false;
        this.route.params.forEach(params => this.id = params['id']);
        this.scene = this.textEngineService.getScene(this.id);
        this.title = " " + this.id;
        this.eventType = (this.scene.getValue('PageType') === "SingleSelect") ? 'singleselect' :
            ((this.scene.getValue('PageType') === "MultiSelect") ? 'multiselect' : 'unknown');

        let self = this;

        this.router.events.subscribe((event) => {
            if (event instanceof NavigationEnd) {
                self.route.params.forEach(params => self.id = params['id']);
                self.title += " " + self.id;
                self.scene = self.textEngineService.getScene(self.id);
                self.eventType = (self.scene.getValue('PageType') === "SingleSelect") ? 'singleselect' :
                    ((self.scene.getValue('PageType') === "MultiSelect") ? 'multiselect' : 'unknown');
            }
        });

        this.subscription = this.calcService.getObservable().subscribe(() => {
            this.answer = Number(this.calcService.getValue("tlinputDec" + this.scene.getValue("ID"))) || -1;
        });

        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
    }

    ngOnDestroy() {
        // this.isClosing = false;
        this.subscription.unsubscribe();
    }

    ngAfterViewInit() {
        this.showModal();
    }

    onClose() {
        this.isClosing = true;
        setTimeout(() => this.router.navigateByUrl(this.utils.stripOutletFromUrl(this.router.url, MEETINGS_OUTLET)), 1000);
    }

    isReadyToSubmit(): boolean {
        return (this.chooser && this.chooser.readyToSubmit !== undefined) ? this.chooser.readyToSubmit : false;
    }

    onSubmit() {
        // submit decision on the chooser component
        if (!this.userIsForeman) {
            return
        }
        this.chooser.submit();
        // now navigate to feedback
        this.locationService.setLocation(this.utils.generateFeedbackUrl(this.id), 1);
        this.router.navigate(this.utils.generateFeedbackUrl(this.id));
        this.hideModal();
    }

    showModal() {
        this.childModal.openModal();
    }

    hideModal() {
        this.childModal.closeModal();
    }
}
