import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, SyncService, StorageService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from '../../shared/location.service';
import { environment } from 'src/environments/environment';
import { INTRO_COMPLETE, HIDE_DASHBOARD_METRICS, Selection_Pending } from '../../shared/utils/utils';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'goal-setting',
    templateUrl: './goalsetting.html',
    styleUrls: ['./goalsetting.styl']
})
export class GoalSettingComponent implements OnInit, OnDestroy {

    private subscription: any;
    isIntro: boolean = true;
    public selectionPending = false;
    goalsRangeList = ['tlInputGoal1_R1', 'tlInputGoal2_R1', 'tlInputGoal5_R1', 'tlInputGoal3_R1', 'tlInputGoal4_R1'];


    constructor(private calcService: CalcService, private router: Router, private locationService: LocationService, private syncService: SyncService, private storageService: StorageService, private appConfig: AppConfig, private communicator: CommunicatorService) { };

    ngOnInit() {

        this.isIntro = this.router.url.indexOf('intro') > -1 ? true : false;
        if(!this.isIntro){
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
        }
        // this.subscription = this.calcService.getObservable().subscribe(() => {
        // // });
        this.communicator.getEmitter(Selection_Pending).subscribe(res => {
            this.selectionPending = ((res + '') === 'true') ? true : false;
          });
        
    }

    onClose() {
        // this.router.navigateByUrl(DASHBOARD_PATH);
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }

        if(!this.isIntro){
            this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
        }
    }

    navigate() {
        this.calcService.setValue('tlInputPasswordLocation', 1);
        let url = ['password'];
        this.storageService.setValue(INTRO_COMPLETE, 'true')

        this.locationService.setLocation(url, 0);
        this.router.navigate(url);
    }

    hideGoalSetting() {
        this.router.navigate(['/dashboard']);
    }

}
