import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, HostListener, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { StorageService } from 'src/app/shared/storage.service';
import { LocationService } from '../../../shared/location.service';
import { Utils } from '../../../shared/utils/utils';
import { MEETING_STATUS, MESSAGE_STATUS } from '../messagesdash/messages-dashboard';
import { LinkStatus } from './linkstatus';
import { LinkTimerComponent } from '../link-timer/linktimer.component';
import { AppConstants } from 'src/app/app-constants';
// timer to update the link in messages = 10s
const LINK_UPDATE_INTERVAL = 10000;
export const MESSAGE_LINK_ADDED_EVENT = "messagelinkadded";

const KEY_NUM_1 = 49;
@Component({
    selector: 'message-link',
    templateUrl: './link.html',
    styleUrls: ['./link.styl']
})

export class LinkComponent implements OnInit, OnDestroy {
    @Input() data: any;
    @Input() index: any;
    @Input() type: string;
    @ViewChild('linkTimer', {static: false}) timerRef: LinkTimerComponent;
    @HostListener('document:keyup', ['$event'])
    triggerShortcuts($event: KeyboardEvent) {
        if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_1) {
            this.forceTrigger();
        }
    }
    
    triggerListener: Subscription;
    protected observable: Subscription;
    public isHidden = true;
    public isViewed = false;
    private _timeout: any = null;

    linkTimerValue: number;
    linkTimerStarted = false;
    linkTimerCompleted = false;

    constructor(private calcService: CalcService, private router: Router, private utils: Utils, private storageService: StorageService,
        private locationService: LocationService, private communicator: CommunicatorService) { }

    ngOnInit() {
        this.observable = this.calcService.getObservable().subscribe((result) => {

            // console.log("Calculation complete ---- " + window.performance.now());
            this._timeout = setTimeout(() => this.updateLink(), LINK_UPDATE_INTERVAL);
        });

        this.triggerListener = this.communicator.getEmitter(AppConstants.EVENTS.MANUALLY_REDUCE_TIMER_DELAY).subscribe(()=>{
            this.forceTrigger()
        })

        this.data["icon"] = "assets/images/" + this.data["icon"];
        this.data["image"] = "assets/images/" + this.data["image"];

        this.updateLink();
    }

    onSelect() {
        let childUrl: string = this.type + '/' + this.data.ID;
        let key = (this.type === "meeting") ? MEETING_STATUS : MESSAGE_STATUS,
            linkStatus = this.storageService.getValueFromDictionary(key);
        if (!this.isViewed) {
            linkStatus.markVisited();
            this.isViewed = true;
        }
        this.communicator.trigger(key, this.storageService.getValueFromDictionary(key));
        // this.router.navigate(['/dashboard', 'meetings', this.meeting.ID]);
        let url = ['dashboard/', { outlets: { 'secondary-nav': childUrl } }];
        // let url = 'dashboard/'+ childUrl;
        this.locationService.setLocation(url, 1);
        this.router.navigate(url);
    }

    updateLink(isInit?: boolean) {
        if(this.linkTimerStarted){
            return;
        }
        let calcTriggerStatus: any = this.calcService.getValue(this.data.triggerRange);
        let isHidden = (calcTriggerStatus != 1);
        let key = (this.type === "meeting") ? MEETING_STATUS : MESSAGE_STATUS,
            linkStatus = this.storageService.getValueFromDictionary(key);

        this.isViewed = (Number(this.calcService.getValue("tlinputRead" + this.data.ID)) !== 0);
        // not the initial update - and isHidden is false (link is visible as per model) and is not already visible on page
        if (!isInit && isHidden === false && this.isHidden !== isHidden) {
            if(this.data.time && this.data.time > 0){
                this.linkTimerValue = this.data.time * 60;
                this.linkTimerStarted = true;
            }else{
                this.onTimeUp();
            }
        }
        else if (isInit) {
            linkStatus.addLink(this.data.triggerRange, isHidden, this.isViewed);
        }
        this.isHidden = isHidden;
        this.communicator.trigger(key, this.storageService.getValueFromDictionary(key));
    }

    ngOnDestroy() {
        clearTimeout(this._timeout);
        this.observable.unsubscribe();
        if(this.triggerListener){
            this.triggerListener.unsubscribe()
        }
    }

    onTimeUp(){
        this.linkTimerCompleted = true;
        let key = (this.type === "meeting") ? MEETING_STATUS : MESSAGE_STATUS;
        let linkStatus = this.storageService.getValueFromDictionary(key);
        this.communicator.trigger(MESSAGE_LINK_ADDED_EVENT, this.data);
        if (!linkStatus._links[this.data.triggerRange]) {
            linkStatus.addLink(this.data.triggerRange, false, this.isViewed);
        }
        else {
            linkStatus.updateLink(this.data.triggerRange, false, this.isViewed);
        }

        if(this.triggerListener){
            this.triggerListener.unsubscribe();
        }
    }

    forceTrigger(){
        if(this.linkTimerStarted && !this.linkTimerCompleted){
            this.timerRef.updateTimer(1)
        }
    }
}
