import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, TextService, SyncService, NumberFormatting as numberFormatting } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Subscription } from 'rxjs';
import { LocationService } from '../../shared/location.service';
import { StorageService } from '../../shared/storage.service';
import { EOR_FEEDBACK, INTRO_COMPLETE, PASSWORD_VISIBLE_AFTERFEEDBACK_R1, PASSWORD_VISIBLE_AFTERFEEDBACK_R2, PASSWORD_VISIBLE_AFTERINTRO, PASSWORD_VISIBLE_AFTERSUBMIT_R1, PASSWORD_VISIBLE_AFTERSUBMIT_R2, PASSWORD_VISIBLE_AFTERSUBMIT_R3, ROUTES, SCENARIO, HIDE_DASHBOARD_METRICS, HIDE_NAVIGATION, FLAG_SKIP_PASSWORD_REF } from '../../shared/utils/utils';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';

@Component({
    selector: 'im-password',
    templateUrl: './password.component.html',
    styleUrls: ['./password.component.styl']
})

export class PasswordComponent {
    @ViewChild('passwordModal', { static: true }) public passwordModal: ModalDirective;

    private selfRef;
    private name = "";
    private isViewed = false;
    private currentYear = 1;
    private fbYear = 0;
    private clearToSubmit = false;
    private warningMessages = "";
    private errorMessages = "";
    private defaultPassword = "bts287";

    private teamNumber = 0;
    private passwordToSubmit_AfterIntro = this.textengineservice.getText("Password_AfterIntro");
    private passwordToSubmit_AfterSubmit_R1 = this.textengineservice.getText("Password_AfterSubmit_R1");
    private passwordToSubmit_AfterFeedback_R1 = this.textengineservice.getText("Password_AfterFeedback_R1");
    private passwordToSubmit_AfterSubmit_R2 = this.textengineservice.getText("Password_AfterSubmit_R2");
    private passwordToSubmit_AfterFeedback_R2 = this.textengineservice.getText("Password_AfterFeedback_R2");
    private passwordToSubmit_AfterSubmit_R3 = this.textengineservice.getText("Password_AfterSubmit_R3");

    private passwordToCancel_AfterIntro = this.textengineservice.getText("CancelPassword_AfterIntro");
    private passwordToCancel_AfterSubmit_R1 = this.textengineservice.getText("CancelPassword_AfterSubmit_R1");
    private passwordToCancel_AfterFeedback_R1 = this.textengineservice.getText("CancelPassword_AfterFeedback_R1");
    private passwordToCancel_AfterSubmit_R2 = this.textengineservice.getText("CancelPassword_AfterSubmit_R2");
    private passwordToCancel_AfterFeedback_R2 = this.textengineservice.getText("CancelPassword_AfterFeedback_R2");
    private passwordToCancel_AfterSubmit_R3 = this.textengineservice.getText("CancelPassword_AfterSubmit_R3");

    public submitPassword = "";
    private passwordVisible = 0;
    public showPasswordError = false;
    private activeReportRoute: boolean = false;
    private endSessionVisible = false;
    private subscription: Subscription;
    private calcServiceSubscription: Subscription;
    public userIsForeman = true;
    passwordPlaceholder: string = '';
    constructor(private storageService: StorageService, private router: Router, private textengineservice: TextService, private calcService: CalcService,
        private locationService: LocationService, private communicator: CommunicatorService,private syncService: SyncService,  private appConfig: AppConfig) { };

    ngOnInit() {
        let self = this;
        this.selfRef = self;
        if (this.appConfig.configuration.connectToPulse) {
            this.userIsForeman = this.syncService.isParticipantIsForeMan('tlInputTeamName');
        }
        this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
        this.subscription = this.communicator.getEmitter(SCENARIO).subscribe((val) => {
        });

        this.passwordVisible = 1;

        let showPassword = +this.calcService.getValue(FLAG_SKIP_PASSWORD_REF) == 0
        if(showPassword){
            let locationNumber = +this.calcService.getValue("tlInputPasswordLocation");
            let PASSWORD_RANGE;
            switch(locationNumber){
                case 1: PASSWORD_RANGE = "tlInputPasswordAfterIntro"; break;
                case 2: PASSWORD_RANGE = "tlInputPasswordAfterSubmit_R1"; break;
                case 3: PASSWORD_RANGE = "tlInputPasswordAfterFeedback_R1"; break;
                case 4: PASSWORD_RANGE = "tlInputPasswordAfterSubmit_R2"; break;
                case 5: PASSWORD_RANGE = "tlInputPasswordAfterFeedback_R2"; break;
                case 6: PASSWORD_RANGE = "tlInputPasswordAfterSubmit_R3"; break;
                case 7: PASSWORD_RANGE = "tlInputPasswordAfterFeedback_R3"; break;
            }
            let skipThisPassword = +this.calcService.getValue(PASSWORD_RANGE) == 0;
            if(skipThisPassword){
                this.submitPassword = this.defaultPassword;
                this.hideSubmit(true)
            }else{

            }
            this.showSubmit();
        }else{
            this.submitPassword = this.defaultPassword;
            this.hideSubmit(true)
        }
        this.passwordPlaceholder = this.textengineservice.getTextForYear('EnterPassword', 'tlInputCurrentRound');
    }

    ngAfterViewInit() {
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    showSubmit() {
        let errorInitiatives = false;
        let initNum = 1;
        this.warningMessages = "";
        this.errorMessages = "";
        this.submitPassword = "";

        // console.log(this.dataStore.getData(LOCATION));
        this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'true');
        // mark introviewed - and persist
        this.storageService.setValue(INTRO_COMPLETE, 'true');
    }

    hideSubmit(confirmed: boolean = false) {
        let closeSubmit = true;

        if (confirmed) {
            let matchedDefault = (this.submitPassword == this.defaultPassword);
            let passwordMatchAfterIntro = (this.submitPassword == this.passwordToSubmit_AfterIntro) || matchedDefault;
            let passwordMatchAfterSubmit_R1 = (this.submitPassword == this.passwordToSubmit_AfterSubmit_R1) || matchedDefault;
            let passwordMatchAfterFeedback_R1 = (this.submitPassword == this.passwordToSubmit_AfterFeedback_R1) || matchedDefault;
            let passwordMatchAfterSubmit_R2 = (this.submitPassword == this.passwordToSubmit_AfterSubmit_R2) || matchedDefault;
            let passwordMatchAfterFeedback_R2 = (this.submitPassword == this.passwordToSubmit_AfterFeedback_R2) || matchedDefault;
            let passwordMatchAfterSubmit_R3 = (this.submitPassword == this.passwordToSubmit_AfterSubmit_R3) || matchedDefault;

            let cancelPasswordMatch_AfterIntro = (this.submitPassword == this.passwordToCancel_AfterIntro) || matchedDefault;
            let cancelPasswordMatch_AfterSubmit_R1 = (this.submitPassword == this.passwordToCancel_AfterSubmit_R1) || matchedDefault;
            let cancelPasswordMatch_AfterFeedback_R1 = (this.submitPassword == this.passwordToCancel_AfterFeedback_R1) || matchedDefault;
            let cancelPasswordMatch_AfterSubmit_R2 = (this.submitPassword == this.passwordToCancel_AfterSubmit_R2) || matchedDefault;
            let cancelPasswordMatch_AfterFeedback_R2 = (this.submitPassword == this.passwordToCancel_AfterFeedback_R2) || matchedDefault;
            let cancelPasswordMatch_AfterSubmit_R3 = (this.submitPassword == this.passwordToCancel_AfterSubmit_R3) || matchedDefault;

            this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
            this.fbYear = parseInt(this.calcService.getValue("tlInputFeedbackRound"));
            let locationNumber: any = this.calcService.getValue("tlInputPasswordLocation");

            if (passwordMatchAfterIntro && this.currentYear == 1 && locationNumber == 1) {
                this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
                let maxYear = parseInt(this.calcService.getValue("tlInputMaxRound"));

                // this.calcService.setValue("tlInputCurrentRound", this.currentYear);

                if (this.currentYear <= maxYear) {
                    this.passwordVisible = 0;
                    this.calcService.setValue(PASSWORD_VISIBLE_AFTERINTRO, this.passwordVisible);

                    this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
                    // mark introviewed - and persist
                    this.storageService.setValue(INTRO_COMPLETE, 'true');
                    this.locationService.setLocation(ROUTES.DASHBOARD, 1);
                    this.router.navigateByUrl(ROUTES.DASHBOARD);
                } else {
                    // TODO: navigate to "thanks for playing" screen
                }
            }
            else if (passwordMatchAfterSubmit_R1 && this.currentYear == 1 && locationNumber == 2) {
                this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
                let maxYear = parseInt(this.calcService.getValue("tlInputMaxRound"));

                // this.calcService.setValue("tlInputCurrentRound", this.currentYear);

                if (this.currentYear <= maxYear) {
                    this.passwordVisible = 0;
                    this.calcService.setValue(PASSWORD_VISIBLE_AFTERSUBMIT_R1, this.passwordVisible);
                    let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                    this.calcService.setValue("tlInputFeedbackRound", tlInputCurrentRound);
                    this.calcService.setValueForYear("tlInputSubmitted", 0, "tlInputCurrentRound");
                    this.communicator.trigger(HIDE_NAVIGATION, 'true');
                    this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'true');

                    let url = [EOR_FEEDBACK, tlInputCurrentRound ];
                    this.locationService.setLocation(url, 1);
                    this.router.navigate(url);
                } else {
                    // TODO: navigate to "thanks for playing" screen
                }
            }
            else if (passwordMatchAfterFeedback_R1 && this.currentYear == 1 && locationNumber == 3) {
                this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
                let maxYear = parseInt(this.calcService.getValue("tlInputMaxRound"));

                if (this.currentYear <= maxYear) {
                    this.copyDecisionsForward();
                }
                this.currentYear = this.currentYear + 1;
                this.calcService.setValue("tlInputCurrentRound", this.currentYear);

                if (this.currentYear <= maxYear) {
                    this.passwordVisible = 0;
                    this.calcService.setValue(PASSWORD_VISIBLE_AFTERFEEDBACK_R1, this.passwordVisible);
                    let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                    this.calcService.setValue("tlInputFeedbackRound", tlInputCurrentRound);
                    this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
                    this.communicator.trigger(HIDE_NAVIGATION, 'false');

                    // mark introviewed - and persist
                    this.storageService.setValue(INTRO_COMPLETE, 'true');
                    this.locationService.setLocation(ROUTES.DASHBOARD, 1);
                    this.router.navigateByUrl(ROUTES.DASHBOARD);
                } else {
                    // TODO: navigate to "thanks for playing" screen
                }
            }
            else if (passwordMatchAfterSubmit_R2 && this.currentYear == 2 && locationNumber == 4) {
                this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
                let maxYear = parseInt(this.calcService.getValue("tlInputMaxRound"));

                // this.calcService.setValue("tlInputCurrentRound", this.currentYear);

                if (this.currentYear <= maxYear) {
                    this.passwordVisible = 0;
                    this.calcService.setValue(PASSWORD_VISIBLE_AFTERSUBMIT_R2, this.passwordVisible);
                    let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                    this.calcService.setValue("tlInputFeedbackRound", tlInputCurrentRound);
                    this.calcService.setValueForYear("tlInputSubmitted", 0, "tlInputCurrentRound");
                    this.communicator.trigger(HIDE_NAVIGATION, 'true');
                    this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'true');

                    let url = [EOR_FEEDBACK,  tlInputCurrentRound ];
                    this.locationService.setLocation(url, 1);
                    this.router.navigate(url);
                } else {
                    // TODO: navigate to "thanks for playing" screen
                }
            }
            else if (passwordMatchAfterFeedback_R2 && this.currentYear == 2 && locationNumber == 5) {
                this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
                let maxYear = parseInt(this.calcService.getValue("tlInputMaxRound"));

                if (this.currentYear <= maxYear) {
                    this.copyDecisionsForward();
                }
                this.currentYear = this.currentYear + 1;
                this.calcService.setValue("tlInputCurrentRound", this.currentYear);

                if (this.currentYear <= maxYear) {
                    this.passwordVisible = 0;
                    this.calcService.setValue(PASSWORD_VISIBLE_AFTERFEEDBACK_R2, this.passwordVisible);
                    let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                    this.calcService.setValue("tlInputFeedbackRound", tlInputCurrentRound);
                    this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
                    this.communicator.trigger(HIDE_NAVIGATION, 'false');

                    // mark introviewed - and persist
                    this.storageService.setValue(INTRO_COMPLETE, 'true');
                    this.locationService.setLocation(ROUTES.DASHBOARD, 1);
                    this.router.navigateByUrl(ROUTES.DASHBOARD);
                } else {
                    // TODO: navigate to "thanks for playing" screen
                }
            }
            else if (passwordMatchAfterSubmit_R3 && this.currentYear == 3 && locationNumber == 6) {
                this.currentYear = parseInt(this.calcService.getValue("tlInputCurrentRound"));
                let maxYear = parseInt(this.calcService.getValue("tlInputMaxRound"));

                // this.calcService.setValue("tlInputCurrentRound", this.currentYear);

                this.calcService.setValue("tlInputSimComplete", 1);
                if (this.currentYear <= maxYear) {
                    this.passwordVisible = 0;
                    this.calcService.setValue(PASSWORD_VISIBLE_AFTERSUBMIT_R3, this.passwordVisible);
                    let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                    this.calcService.setValue("tlInputFeedbackRound", tlInputCurrentRound);
                    this.calcService.setValueForYear("tlInputSubmitted", 0, "tlInputCurrentRound");
                    this.communicator.trigger(HIDE_NAVIGATION, 'true');
                    this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'true');

                    let url = [EOR_FEEDBACK, tlInputCurrentRound ];
                    this.locationService.setLocation(url, 1);
                    this.router.navigate(url);
                } else {
                    // TODO: navigate to "thanks for playing" screen
                }
            }
            else if (cancelPasswordMatch_AfterIntro && locationNumber == 1) {
                this.passwordVisible = 0;
                this.calcService.setValue(PASSWORD_VISIBLE_AFTERINTRO, this.passwordVisible);
                this.submitPassword = "";
                this.calcService.setValue('tlInputPasswordLocation', parseInt(locationNumber) - 1);
                let url = "intro/goalsetting";
                this.locationService.setLocation(url, 1);
                this.router.navigateByUrl(url);
            }
            else if (cancelPasswordMatch_AfterSubmit_R1 && locationNumber == 2) {
                this.passwordVisible = 0;
                this.calcService.setValue(PASSWORD_VISIBLE_AFTERSUBMIT_R1, this.passwordVisible);
                this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
                this.communicator.trigger(HIDE_NAVIGATION, 'false');

                this.calcService.setValue('tlInputPasswordLocation', parseInt(locationNumber) - 1);
                // mark introviewed - and persist
                this.storageService.setValue(INTRO_COMPLETE, 'true');
                this.submitPassword = "";
                this.locationService.setLocation(ROUTES.DASHBOARD, 1);
                this.router.navigateByUrl(ROUTES.DASHBOARD);
            } else if (cancelPasswordMatch_AfterFeedback_R1 && locationNumber == 3) {
                this.passwordVisible = 0;
                this.calcService.setValue(PASSWORD_VISIBLE_AFTERFEEDBACK_R1, this.passwordVisible);
                let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                this.submitPassword = "";

                this.calcService.setValue('tlInputPasswordLocation', parseInt(locationNumber) - 1);
                this.calcService.setValue("tlInputPasswordAfterFeedback_R1", 1);
                let url = [EOR_FEEDBACK, tlInputCurrentRound ];
                this.locationService.setLocation(url, 1);
                this.router.navigate(url);
            }
            else if (cancelPasswordMatch_AfterSubmit_R2 && locationNumber == 4) {
                this.passwordVisible = 0;
                this.calcService.setValue(PASSWORD_VISIBLE_AFTERSUBMIT_R2, this.passwordVisible);
                this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
                // mark introviewed - and persist
                this.storageService.setValue(INTRO_COMPLETE, 'true');

                this.calcService.setValue('tlInputPasswordLocation', parseInt(locationNumber) - 1);
                this.submitPassword = "";
                this.locationService.setLocation(ROUTES.DASHBOARD, 1);
                this.router.navigateByUrl(ROUTES.DASHBOARD);
            }
            else if (cancelPasswordMatch_AfterFeedback_R2 && locationNumber == 5) {
                this.passwordVisible = 0;
                this.calcService.setValue(PASSWORD_VISIBLE_AFTERFEEDBACK_R2, this.passwordVisible);
                let tlInputCurrentRound = this.calcService.getValue("tlInputCurrentRound");
                this.submitPassword = "";

                this.calcService.setValue('tlInputPasswordLocation', parseInt(locationNumber) - 1);
                this.calcService.setValue("tlInputPasswordAfterFeedback_R2", 1);
                let url = [EOR_FEEDBACK, tlInputCurrentRound ];
                this.locationService.setLocation(url, 1);
                this.router.navigate(url);
            }
            else if (cancelPasswordMatch_AfterSubmit_R3 && locationNumber == 6) {
                this.passwordVisible = 0;
                this.calcService.setValue(PASSWORD_VISIBLE_AFTERSUBMIT_R3, this.passwordVisible);
                this.calcService.setValue('tlInputPasswordLocation', parseInt(locationNumber) - 1);
                this.communicator.trigger(HIDE_DASHBOARD_METRICS, 'false');
                // mark introviewed - and persist
                this.storageService.setValue(INTRO_COMPLETE, 'true');
                this.submitPassword = "";
                this.locationService.setLocation(ROUTES.DASHBOARD, 1);
                this.router.navigateByUrl(ROUTES.DASHBOARD);
            } else {
                // incorrect password
                this.showPasswordError = true;
                this.submitPassword = "";
                closeSubmit = false;

                this.errorMessages = this.textengineservice.getText("PasswordErrorMessage");
                console.log("location: " + locationNumber + " - " + this.errorMessages);
            }
        } else {
            this.passwordVisible = 0;
            this.calcService.setValue(PASSWORD_VISIBLE_AFTERINTRO, this.passwordVisible);
            this.submitPassword = "";
        }
    }

    // this function is called before the round number is updated to set some of the next round's decisions to the 
    // decisions the users made
    copyDecisionsForward() {
        let cy = this.currentYear;
        let ny = (this.currentYear + 1);
        this.calcService.setValue("tlInputProd1Price_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd1Price_R"+cy))));
        this.calcService.setValue("tlInputProd1Units_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd1Units_R"+cy))));
        this.calcService.setValue("tlInputProd1Discount_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd1Discount_R"+cy))));
        this.calcService.setValue("tlInputSFCoverage_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputSFCoverage_R"+cy))));
        this.calcService.setValue("tlInputProd2Price_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd2Price_R"+cy))));
        this.calcService.setValue("tlInputProd2Units_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd2Units_R"+cy))));
        this.calcService.setValue("tlInputProd2Discount_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd2Discount_R"+cy))));
        this.calcService.setValue("tlInputProd3Price_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd3Price_R"+cy))));
        this.calcService.setValue("tlInputProd3Units_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd3Units_R"+cy))));
        this.calcService.setValue("tlInputProd3Discount_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd3Discount_R"+cy))));
        this.calcService.setValue("tlInputProd3ChannelIncent_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd3ChannelIncent_R"+cy))));
        this.calcService.setValue("tlInputProd4Price_R"+ny, this.calcService.getValue("tlInputProd4Price_R"+cy));
        this.calcService.setValue("tlInputProd5Price_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd5Price_R"+cy))));
        this.calcService.setValue("tlInputProd5Units_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd5Units_R"+cy))));
        this.calcService.setValue("tlInputProd5Discount_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd5Discount_R"+cy))));
        this.calcService.setValue("tlInputPlannedUtiliz_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputPlannedUtiliz_R"+cy))));
        this.calcService.setValue("tlInputProd5Outsource_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd5Outsource_R"+cy))));
        this.calcService.setValue("tlInputProd3Ftr1_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProd3Ftr1_R"+cy))));
        // this.calcService.setValue("tlInputProcess_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProcess_R"+cy))));
        this.calcService.setValue("tlInputBrand_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputBrand_R"+cy))));
        this.calcService.setValue("tlInputProductionOutsource_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputProductionOutsource_R"+cy))));
        this.calcService.setValue("tlInputEmp2Change_R"+ny, Number(numberFormatting.unformat(this.calcService.getValue("tlInputEmp2Change_R"+cy))));
    }
}
